.aside-commercial-wrap {
  position: relative;

  .close-btn {
    position: absolute;
    right: $gutter;
    top: $gutter;
    width: 28px;
    height: 28px;
    background-color: rgba(#262b33, 0.8);
    border-radius: 50%;
    padding: 2px;

    i {
      color: $color-white;
      font-size: 17px;
    }
  }
}
.popular-commercial-picture {
  border-radius: $border-radius * 2;
  overflow: hidden;
}
