.popular-call-action,
.popular-call-action-base {
  .call-action-box {
    margin-top: $gutter * 3;
    @include flex(center, center);

    .call-action-btn {
      padding: $gutter $gutter * 3;
      background-color: $color-white;
      box-shadow: 0 3px 6px rgba(#000, 0.16);
      border-radius: 6px;
      overflow: hidden;
      &:hover {
        a {
          text-decoration: none;
        }

        background-color: rgba($color-white, 0.85);
      }

      > a {
        @include flex(center);
      }

      span {
        font-size: 16px;
        display: inline-block;
        text-align: center;
        margin-top: -2px;
      }

      img {
        width: 30px;
        flex-shrink: 0;
        margin-right: $gutter * 2;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .call-action-btn {
      &:nth-of-type(1) {
        margin-right: $gutter * 2;
      }

      &:nth-of-type(2) {
        margin-left: $gutter * 2;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    border-radius: $gutter;
    overflow: hidden;
  }

  @include media-breakpoint-up(md) {
    h3 {
      font-size: 30px;
    }
  }

  @include media-breakpoint-down(sm) {
    h3 {
      margin-bottom: $gutter * 2 !important;
    }
    .call-action-box {
      flex-wrap: wrap;

      .call-action-btn {
        width: 100%;
        max-width: 400px;

        > a {
          justify-content: center;
        }

        margin: {
          left: 0 !important;
          right: 0 !important;
        }
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .call-action-box {
      .call-action-btn {
        padding: {
          left: $gutter + 2px;
          right: $gutter + 2px;
        }

        span {
          font-size: 15px;
        }
      }
    }
  }
}

.popular-call-action {
  background-color: $color-card;

  > .container {
    @include flex(center, center, wrap);
    padding: $gutter * 4 $gutter * 3 46px $gutter * 3;
  }

  h3 {
    text-align: center;
  }

  .desc {
    text-align: center;
  }

  @include media-breakpoint-up(md) {
    > .container {
      padding: $gutter * 4 60px 46px 60px;
    }
  }

  @include media-breakpoint-up(lg) {
    > .container {
      padding: $gutter * 4 114px $gutter * 5 114px;
    }
  }

  @include media-breakpoint-up(xl) {
    .call-action-btn {
      &:nth-of-type(1) {
        margin-right: $gutter * 3;
      }

      &:nth-of-type(2) {
        margin-left: $gutter * 3;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .call-action-btn {
      &:nth-of-type(1) {
        margin-bottom: $gutter * 3;
      }
    }
  }

  @include media-breakpoint-down(mc) {
    > .container {
      padding: {
        left: $gutter * 2;
        right: $gutter * 2;
      }
    }
  }
}

.popular-call-action-base {
  .call-action-box {
    .call-action-btn {
      border-radius: 50px;
    }
  }

  @include media-breakpoint-up(md) {
    .call-action-box {
      justify-content: flex-start;
    }
  }

  @include media-breakpoint-up(xl) {
    .call-action-btn {
      &:nth-of-type(1) {
        margin-right: 12px;
      }

      &:nth-of-type(2) {
        margin-left: 12px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    .call-action-box {
      flex-wrap: wrap;

      .call-action-btn {
        margin-left: 0 !important;
        margin-right: 0 !important;
        &:nth-of-type(1) {
          margin-bottom: $gutter * 2;
        }
      }
    }
  }

  @media (max-width: 460px) {
    padding-bottom: 50vw !important;
  }
}

.mb-screen {
  @include media-breakpoint-down(lg) {
    padding: 0;
  }
}
