@mixin richVideoListLayout3() {
  .title {
    font-size: 16px;
    font-weight: 500;
    color: #292929;
    line-height: 1.33em;
    margin-left: -8px;
    @include limit-line(3, 1.33);
  }
  .rich-slick-card {
    margin-bottom: 24px;
  }
}

.tabs-slick.audios-card-slick {
  .rich-slick-card {
    .card-body {
      .title {
        margin-top: 16px;
        line-height: 1.4em;
        font-size: 18px;
        @include limit-line(3, 1.4);
      }
    }
    .cover {
      position: relative;
      &::before,
      &::after {
        display: none !important;
      }
      a {
        position: relative;
        display: block;
        padding-top: 56.25%;
        background-color: #f4f4f4;
        overflow: hidden;
        border-radius: 4px;
        img {
          transition: transform 0.8s ease, opacity 2s ease-in-out;
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          object-fit: cover;
          object-position: center;
          backface-visibility: hidden;
        }
      }
      &:hover {
        img {
          transform: scale(1.05);
        }
      }
    }

    .duration {
      right: 4px;
      bottom: 4px;
      transition: opacity 0.35s ease;
      border-radius: 4px;
      padding: 3px 6px;
      padding-left: 24px;
      background-color: rgba(0, 0, 0, 0.6);
      line-height: 1em;
      display: flex;
      align-items: center;
      position: absolute;
      font-size: 12px;
      color: #fff;
      &::after {
        content: "\e921";
        font-family: "icomoon" !important;
        speak: never;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        position: absolute;
        left: 6px;
        top: 3px;
      }
    }
  }

  .rich-slick-item {
    &.layout-3 {
      .top {
        .title {
          margin-top: 12px;
          font-size: 18px;
          font-weight: 500;
          color: #292929;
          line-height: 1.2em;
          @include limit-line(2, 1.2);
        }

        .rich-slick-card {
          margin-bottom: 24px;
        }
      }

      .bottom {
        @include richVideoListLayout3();
      }
    }
  }
}
