.sidebar-box {
  .article-box.layout-6 {
    .article-list {
      .article-card {
        .article-card-title {
          font-size: 16px;
          font-weight: normal;
          @include limit-line(3, 1.3);
        }
      }
    }

    @include media-breakpoint-up(xl) {
      .article-card:nth-of-type(1) {
        margin-bottom: $gutter * 5;
      }
    }
  }
}
