.panel {
  .panel-header {
    width: 100%;
    .icon-navigate-next {
      font-size: 28px;
      transform: translateY(2px);
    }

    a:not(.more):hover {
      text-decoration: none;
    }
  }

  .level-title {
    text-decoration: none;
    @include flex();
    margin: -$gutter auto $gutter * 3 auto;
    font-size: 18px;
    a {
      font-size: inherit;
    }
  }

  &.plan1 {
    margin-bottom: $gutter * 6;

    .panel-header {
      margin-bottom: $gutter * 4;
      > a {
        justify-content: space-between;

        &:hover {
          h2,
          i {
            color: $color-text-hover;
          }
        }
      }
    }

    @include media-breakpoint-up(md) {
      margin-bottom: $gutter * 9;

      .panel-header {
        justify-content: center;
        margin-bottom: $gutter * 5;

        h2 {
          padding-right: 4px;
          text-align: center;
        }
      }
    }

    @include media-breakpoint-up(xl) {
      .panel-header {
        margin-bottom: $gutter * 6;
      }
    }

    @include media-breakpoint-down(sm) {
      .panel-header {
        > a {
          width: 100%;
        }
        h2 {
          max-width: calc(100% - 36px);
        }
      }
    }

    @include media-breakpoint-down(mc) {
      margin-bottom: $gutter * 4;

      .panel-header {
        margin-bottom: $gutter * 2;
      }
    }
  }

  &.plan2 {
    margin-bottom: $gutter * 6;

    .panel-header {
      margin-bottom: $gutter * 4;

      > a:hover {
        h2,
        i {
          color: $color-online;
        }
      }

      h2 {
        position: relative;

        &::before {
          content: "";
          display: block;
          width: 4px;
          height: 32px;
          background-color: #ed7b18;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          margin: auto;
        }
      }
    }

    a.more {
      font-size: 16px;
      line-height: 24px;
      color: $color-online;
      flex-shrink: 0;
      white-space: nowrap;
      @include flex(center, space-between);

      &::after {
        display: block;
        content: "";
        width: 8px;
        height: 8px;
        border: 2px solid $color-online;
        border-left: none;
        border-bottom: none;
        transform: rotateZ(45deg) translateY(2px);
        transform-origin: center;
        margin-left: $gutter;
      }
    }

    @include media-breakpoint-up(xl) {
      margin-bottom: $gutter * 8;
    }

    @include media-breakpoint-up(md) {
      margin-bottom: $gutter * 7;

      .panel-header {
        justify-content: space-between;
        margin-bottom: $gutter * 3;

        h2 {
          padding-left: $gutter * 2;
          padding-right: 12px;
        }
      }
    }

    @include media-breakpoint-down(sm) {
      .panel-header {
        > a {
          width: 100%;
          justify-content: space-between;

          &.more {
            display: none;
          }
        }
        h2 {
          max-width: calc(100% - 36px);
          &::before {
            display: none;
          }
        }
      }
    }

    @include media-breakpoint-down(xs) {
      .panel-header {
        h2 {
          &::before {
            display: block;
            left: -12px;
          }
        }
      }
    }

    @include media-breakpoint-down(mc) {
      .panel-header {
        margin-bottom: $gutter * 2;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .level-title {
      margin-top: -$gutter * 2;
      justify-content: center;
      font-size: 22px;
    }
  }

  @include media-breakpoint-up(xl) {
    .level-title {
      margin-bottom: $gutter * 4;
    }
  }
}

.panel-aside,
.sidebar-part .aside-commercial-wrap {
  margin-bottom: $gutter * 5;

  &:nth-last-child(1) {
    margin-bottom: 0;
  }
}
