.js-search-content {
  width: 100%;

  .input-plate {
    width: 100%;
    background-color: #f4f4f4;
    height: 48px;
    padding: 10px $gutter 10px 12px;
    padding-left: 40px;
    align-items: center;
    position: relative;
    border-radius: $gutter;

    label {
      position: absolute;
      left: 10px;
      top: 14px;
    }

    .search-input {
      border: 0;
      width: 100%;
      max-width: calc(100% - 120px);
      font-size: 16px;
    }

    .clear-btn {
      padding: 0px;
      width: 22px;
      height: 22px;
      background-color: rgba(#262b33, 0.1);
      border-radius: 50%;
      margin-right: 6px;
      flex-shrink: 0;
      visibility: hidden;
      opacity: 0;

      i {
        font-size: 16px !important;
        line-height: 10px;
      }

      &.on {
        visibility: visible;
        opacity: 1;
      }
    }

    .search-change-btn {
      border-left: 1px solid rgba(0, 0, 0, 0.1);
      padding-left: 8px;
      padding-right: 0px;
      -ms-flex-negative: 0;
      flex-shrink: 0;

      span {
        white-space: nowrap;
        font-size: 14px;
        line-height: 22px;
      }

      i {
        font-size: 24px;
        margin-left: $gutter;
      }
    }

    .search-options {
      position: absolute;
      top: 40px;
      right: 0;
      z-index: 4;
      width: 106px;
      padding: 14px 0;
      background-color: $color-white;
      box-shadow: 0 3px 6px rgba(#000, 0.16);
      border-radius: 6px;
      overflow: hidden;
      opacity: 0;
      visibility: hidden;
      transition: all 0.1s ease-in-out;

      &.show {
        opacity: 1;
        visibility: visible;
      }

      button {
        width: 100%;
        padding: $gutter 20px;
        font-size: 14px;
      }
    }
  }

  @include media-breakpoint-up(xs) {
    .input-plate .search-options {
      button.active {
        display: none;
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .input-plate {
      .search-change-btn {
        padding-left: 0;
        span {
          display: none;
        }
        i {
          margin-left: 4px;
        }
      }

      .search-input {
        max-width: calc(100% - 54px);
      }

      .search-options {
        button {
          text-align: left;
          &.active {
            background-color: #f5f5f5;
          }
        }
      }
    }
  }
}
