#banner {
  .banner-carousel-5 {
    &.init {
      .swiper-wrapper {
        @include flex(center, center);
      }
    }

    &:hover {
      .slick-arrow {
        opacity: 1;
      }
    }

    .ban-item {
      width: 100vw;
      //margin: 0 $gutter;
    }

    .swiper-pagination {
      bottom: $gutter * 2;
      span {
        width: 48px;
        height: 3px;
        border-radius: 3px;
        background-color: rgba($color-white, 0.4);
        opacity: 1;

        // &.swiper-pagination-bullet-active {
        //   background-color: $color-white;
        // }
      }
    }

    .slick-arrow {
      &.slick-prev {
        left: $gutter * 2;
      }

      &.slick-next {
        right: $gutter * 2;
      }
    }

    @include media-breakpoint-up(md) {
      &.init {
        .ban-item {
          width: 460px;
          position: relative;
        }
      }

      .ban-item {
        transition: transform 0.35s ease;
        margin: 30px 0;
        transform: scale(0.98) !important;

        .ban-card {
          &::before {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 2;
            pointer-events: none;
            background-color: rgba(#000, 0.3);
            transition: opacity 0.35s ease-in-out;
          }
        }

        &.swiper-slide-active {
          transform: scale(1.16) !important;
          .ban-card {
            &::before {
              opacity: 0;
              visibility: hidden;
            }
          }
        }
      }
    }

    @include media-breakpoint-up(lg) {
      &.init {
        .ban-item {
          width: 520px;
        }
      }
    }

    @include media-breakpoint-up(xl) {
      &.init {
        .ban-item {
          width: 676px;
        }
      }
      .ban-item {
        &.slick-current {
        }
      }
    }

    @include media-breakpoint-down(sm) {
      .ban-item {
        padding: 0 $gutter * 2;
        box-sizing: border-box;

        .ban-card {
          border-radius: $gutter;
          overflow: hidden;
          -webkit-backface-visibility: hidden;
          -moz-backface-visibility: hidden;
          -webkit-transform: translate3d(0, 0, 0);
          -moz-transform: translate3d(0, 0, 0);
        }
      }
    }

    @include media-breakpoint-down(mc) {
      .swiper-pagination {
        span {
          width: 38px;
        }
      }
    }

    &.swiper-container-initialized {
      .swiper-wrapper {
        .ban-item {
          display: block;
        }
      }
    }
  }
}
