@mixin richVideoListLayout3() {
  .title {
    font-size: 16px;
    font-weight: 500;
    color: #292929;
    line-height: 1.33em;
    margin-left: -8px;
    @include limit-line(3, 1.33);
  }
  .rich-slick-card {
    margin-bottom: 24px;
  }
}

.video-box {
  * {
    outline: none !important;
  }

  &[data-layout="default"] {
    .slick-list {
      margin-bottom: -32px;
    }
  }
  &[data-layout="layout-1"],
  &[data-layout="layout-2"] {
    .slick-list {
      margin-bottom: -24px;
    }
  }

  .loading {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 165px;
  }

  @include media-breakpoint-down(xl) {
    .slick-arrow {
      display: none !important;
    }
  }

  .rich-slick-card .cover a {
    padding-bottom: 56.25%;
  }

  .cover {
    position: relative;

    .duration {
      transition: 0.3s;
    }

    @include media-breakpoint-up(xl) {
      &:hover {
        .duration {
          opacity: 0;
        }
      }
    }
  }

  .card-body {
    position: relative;

    .attach {
      position: relative;
      margin-top: 12px;
      @include media-breakpoint-down(mc) {
        display: none;
      }
    }
  }

  .duration {
    position: absolute;
    bottom: 4px;
    right: 4px;
    padding: 2px 6px 2px 20px;
    background: rgba(#14151d, 0.7);
    color: #fff;
    border-radius: 4px;
    font-size: 12px;

    &::after {
      content: "";
      background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAICAYAAAA1BOUGAAAABHNCSVQICAgIfAhkiAAAAGFJREFUCFt1jlENhFAMBGcknSNwgARQADhBAhLuHCABFJQ04REg3H61ne1mjYgV6IFRzfmUERHHtgC1Ohd6heU2Hab1DaYp4z//YKe2T/gDKvWb7wVuQKsOz7YNMKnZ9qYdLn0tJgvHDmUAAAAASUVORK5CYII=");
      background-repeat: no-repeat;
      background-position: center;
      width: 15px;
      height: 15px;
      position: absolute;
      left: 2px;
      top: 2px;
    }
  }

  .rich-slick-item {
    &.layout-1,
    &.layout-2 {
      .left {
        .title {
          margin-top: 16px;
          font-size: 24px;
          font-weight: 700;
          color: #292929;
          line-height: 1.2em;
          @include limit-line(2, 1.2);
        }

        .description {
          font-size: 16px;
          font-weight: 400;
          color: #4c4c4c;
          line-height: 1.4;
          margin-top: 16px;
          @include limit-line(3, 1.4);
        }
      }

      .right {
        .title {
          font-size: 18px;
          font-weight: 500;
          color: #292929;
          line-height: 1.33em;
          margin-top: 16px;
          @include limit-line(3, 1.33);
        }

        .rich-slick-card {
          margin-bottom: 24px;
        }
      }
    }

    &.layout-3 {
      .top {
        .title {
          margin-top: 12px;
          font-size: 18px;
          font-weight: 700;
          color: #292929;
          line-height: 1.2em;
          @include limit-line(2, 1.2);
        }

        .rich-slick-card {
          margin-bottom: 24px;
        }
      }

      .bottom {
        @include richVideoListLayout3();
      }
    }

    &.sidebar {
      @include richVideoListLayout3();
    }

    &.default {
      .rich-slick-card {
        margin-bottom: 32px;
      }

      .title {
        font-size: 18px;
        font-weight: 500;
        color: #292929;
        line-height: 1.33;
        margin-top: 16px;
        @include limit-line(2, 1.33);
      }

      @include media-breakpoint-down(lg) {
        .title {
          margin-top: 12px;
          font-size: 18px;
        }
      }
      @include media-breakpoint-down(mc) {
        .title {
          font-size: 16px;
          margin-top: 8px;
        }
        .rich-slick-card {
          margin-bottom: 16px;
        }
      }
    }
  }

  .is-free-to-scroll-box {
    & > .row {
      flex-wrap: nowrap;
      overflow: scroll;
      margin-left: calc((100vw - 100%) / 2 * -1);
      margin-right: calc((100vw - 100%) / 2 * -1);
      padding-left: calc((100vw - 100%) / 2);

      .is-free-to-scroll {
        margin-right: 24px;
        flex-shrink: 0;
      }

      &::after {
        content: "1";
        display: block;
        width: 1px;
        height: 1px;
        opacity: 0;
        pointer-events: none;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .is-free-to-scroll-box {
      & > .row {
        .is-free-to-scroll {
          margin-right: 16px;
        }
      }
    }
  }
}

.rich-slick-card {
  .cover {
    &::before {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.3);
      border-radius: 4px;
      opacity: 0;
      z-index: 1;
      transition: 0.3s ease-in-out;
    }

    &::after {
      position: absolute;
      content: "";
      background-position: center;
      background-repeat: no-repeat;
      background-image: url(data:image/png;base64,);
      width: 40%;
      padding-top: 21%;
      max-width: 64px;
      min-width: 36px;
      top: 50%;
      left: 50%;
      opacity: 0;
      z-index: 1;
      backface-visibility: hidden;
      transform: translate3d(-50%, -30%, 0);
      transition: 0.3s ease-in-out;
    }

    @include media-breakpoint-up(xl) {
      &:hover {
        &::before {
          opacity: 1;
        }

        &::after {
          opacity: 1;
          transform: translate3d(-50%, -50%, 0);
        }
      }
    }
  }
}

.show-more-video {
  background: #f4f4f4;
  padding-bottom: 56.25%;
  position: relative;
  border-radius: 4px;
  white-space: nowrap;

  a {
    position: absolute;
    left: 50%;
    top: 50%;
    color: #2da140;
    transform: translate(-50%, -50%);
    font-size: 18px;
    line-height: 1.3;

    i {
      color: #2da140;
      display: inline-block;
      transform: translateY(6px);
      pointer-events: none;
    }
  }
  @include media-breakpoint-down(lg) {
    a {
      font-size: 16px;
    }
  }
}
