.article-box.layout-5 {
  .article-list {
    .large-article-card {
      width: 92%;
      .title {
        font-size: 36px;
        font-weight: 700;
        color: #292929;
        line-height: 1.2em;
        margin-bottom: 33px;
      }
      .description {
        font-size: 18px;
        font-weight: 400;
        color: #4c4c4c;
        line-height: 1.33em;
        margin-bottom: 33px;
      }
    }

    .article-card {
      .article-card-body {
        .article-card-title {
          margin-bottom: 32px;
        }
        .article-card-meta {
          &::before {
            top: 50%;
            transform: translateY(-50%);
          }
        }
      }
    }

    @include media-breakpoint-up(xl) {
      & > div:nth-of-type(2) {
        .article-card {
          &:first-of-type {
            display: none;
          }
          &:last-of-type .article-card-body {
            border-bottom: none;
          }
        }
      }
      .article-card.col-xl-6{
        &:nth-last-of-type(1),
        &:nth-last-of-type(2){
          .article-card-body{
            border-bottom: none;
          }
        }
      }
    }
    @include media-breakpoint-between(lg, lg) {
      .article-card {
        .article-card-body {
          .article-card-title {
            font-size: 22px;
            @include limit-line(2, 1.33);
          }
        }
        &:nth-last-of-type(1),
        &:nth-last-of-type(2) {
          .article-card-body {
            border-bottom: none;
          }
        }
      }
    }
    @include media-breakpoint-down(md) {
      .article-card {
        &:nth-last-of-type(1) {
          .article-card-body {
            border-bottom: none;
          }
        }
      }
    }
  }
}
