.rich-slick{position:relative}.rich-slick .slick-list{-webkit-transition:opacity 1.6s;transition:opacity 1.6s;opacity:1}.rich-slick.rending .slick-list{-webkit-transition:opacity 0s;transition:opacity 0s;opacity:0}.rich-slick:hover .slick-arrow{opacity:1;-webkit-user-select:auto;-moz-user-select:auto;-ms-user-select:auto;user-select:auto;pointer-events:auto}.rich-slick.edge-left .slick-prev{opacity:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}.rich-slick.edge-right .slick-next{opacity:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}.rich-slick .rich-slick-lazy{opacity:0;-webkit-transition:opacity 2s ease-in-out;transition:opacity 2s ease-in-out}.rich-slick .rich-slick-lazy.loaded{opacity:1}.rich-slick .slick-slide{margin:0 12px}.rich-slick .slick-list{margin:0 -12px}

