#banner {
  .banner-strip {
    position: relative;
    overflow: hidden;
    height: 210px;

    > .container {
      @include flex(flex-end);
      height: 100%;
    }
  }

  .banner-bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;

    img {
      position: absolute;
      top: 0;
      left: 50%;
      height: 100%;
      max-width: none;
      transform: translateX(-50%);
    }
  }

  .ban-title {
    position: relative;
    width: 100%;
    z-index: 2;
    h1 {
      color: $color-white;
      line-height: 1.2;
      @include limit-line(2, 1.2);
      text-shadow: 0 3px 6px rgba(#000, 0.16);
    }
  }

  @include media-breakpoint-up(md) {
    .banner-strip {
      height: 256px;

      > .container {
        align-items: center;
      }

      .ban-title {
        &.left {
          h1 {
            text-align: left;
          }
        }

        &.center {
          h1 {
            text-align: center;
          }
        }

        &.right {
          h1 {
            text-align: right;
          }
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .banner-strip {
      height: 320px;
    }
  }

  @include media-breakpoint-down(sm) {
    .banner-strip {
      .ban-title {
        padding-bottom: $gutter * 2;
      }
    }
  }
  @include media-breakpoint-down(xs) {
    .banner-strip {
      height: calc(100vw * 0.5626);
    }
  }

  @include media-breakpoint-down(mc) {
    .banner-strip {
      height: 180px;
    }
  }
}
