.category-title {
  margin-bottom: $gutter * 3;
  @include media-breakpoint-up(md) {
    margin-bottom: $gutter * 4;
  }
}

.list-content {
  .main-part {
  }

  .sidebar-part {
    padding-left: 60px;
  }

  @include media-breakpoint-up(xl) {
  }

  @include media-breakpoint-down(lg) {
  }
}

.classify-label {
  margin-bottom: $gutter * 3;
  overflow: hidden;
  width: 100%;
  justify-content: space-between;

  .slider-items {
    .item {
      &.active {
        a {
          color: $color-button-hover;
          border-color: $color-button-hover;
        }
      }
    }
    a {
      font-size: 16px;
    }
  }

  &.open {
    .label-count {
      .slider-items {
        flex-wrap: wrap;
        transform: none !important;

        .item {
          margin-bottom: $gutter * 2;
        }
      }
    }

    .dropdown-btn {
      i {
        transform: rotate(180deg);
      }
    }
  }

  @include media-breakpoint-up(md) {
    margin-bottom: $gutter * 4;

    .label-count {
      width: calc(100% - 60px);
      overflow: hidden;

      .modal-mask,
      .close-btn {
        display: none !important;
      }
    }

    .line-see-more {
      width: 44px;
      height: 44px;
      border-radius: 50%;
      border: 1px solid transparent;
      @include flex(center, center);
      transition: all 0.35s ease;
      padding: 0;
      margin-top: $gutter;

      i {
        font-size: 30px;
        display: inline-block;
        &::before {
          transform: none;
        }
      }

      &:hover {
        border-color: $color-button;
      }
    }

    .dropdown-box {
      position: relative;
      height: 100%;
      &::after {
        content: "";
        width: 110px;
        height: 60px;
        position: absolute;
        top: 0;
        bottom: 0;
        z-index: 9;
        right: 44px;
        background: linear-gradient(
          to right,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 1) 100%
        );
      }
    }

    .slider-items {
      @include flex();
      .item {
        margin-right: $gutter * 2;
        a {
          width: 192px;
          height: 100%;
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    margin-bottom: $gutter * 5;
  }

  @include media-breakpoint-down(sm) {
    .label-count {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 999;
      visibility: hidden;
      opacity: 0;
      pointer-events: none;

      &.show {
        visibility: visible;
        opacity: 1;
        pointer-events: auto;
        transition: all 0.35s ease;
      }

      &.hide {
        visibility: hidden;
        opacity: 0;
        transition: all 0.35s ease;
      }

      .label-wrap {
        position: relative;
        z-index: 15;
        max-width: 556px;
        width: calc(100% - 8px * 4);
        background-color: $color-white;
        margin: auto;
        height: 80vh;
        top: 10vh;
        border-radius: 12px;
        box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14),
          0 9px 46px 8px rgba(0, 0, 0, 0.12),
          0 11px 15px -7px rgba(0, 0, 0, 0.2);

        padding-top: $gutter * 5;
        padding-bottom: $gutter * 3;
        overflow: hidden;
      }

      .slider-items {
        display: block;
        height: 100%;
        overflow-y: auto;
        transform: none !important;
        -webkit-overflow-scrolling: touch;

        li {
          padding: 0 $gutter * 2;
          margin-bottom: $gutter;

          &.active {
            background-color: #f9f9fb;
          }
          a {
            border: 0;
            padding: 10px 0;
          }
        }
      }

      .modal-mask,
      .close-btn {
        position: absolute;
        top: 0;
      }

      .modal-mask {
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 10;
        background-color: rgba(0, 0, 0, 0.6);
      }

      .close-btn {
        right: 2px;
        top: 2px;
        height: 44px;
        width: 44px;
      }
    }

    .current-label-name {
      width: 100%;
      height: 44px;
      background-color: #e7f5e8;
      border-radius: $border-radius;
      @include flex(center, space-between);
      padding: 8px $gutter * 2;

      span {
        @include limit-line(1, 1.5);
        margin-right: 4px;
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .label-wrap {
      width: calc(100% - 8px * 2) !important;
    }
  }
}
