@mixin bg-before() {
  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    background-color: rgba(#000, 0.18);
    display: block;
    width: 100%;
    height: 100%;
  }
}

@import "./ad-article/article-type";
@import "./ad-article/verse-type";

.popular-article,
.popular-verse-of-day {
  background-color: $color-card;

  .pop-content-text {
    padding: 0 36px;
    position: relative;
    &::before,
    &::after {
      content: "";
      display: block;
      width: 45px;
      height: 45px;
      background: {
        position: center center;
        repeat: no-repeat;
        size: 100%;
      }
      position: absolute;
    }

    &::before {
      background-image: url(data:image/png;base64,);
      top: -10px;
      left: 0;
    }
    &::after {
      background-image: url(data:image/png;base64,);
      bottom: -10px;
      right: 0;
    }

    @include media-breakpoint-down(sm) {
      &::before,
      &::after {
        width: 28px !important;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .pop-content-text {
      padding: 0 76px;
    }
  }

  @include media-breakpoint-up(lg) {
    .pop-content-text {
      padding: 0 102px;
    }
  }
}

.popular-verse-of-day-bg,
.popular-article-bg {
  border-radius: $gutter;
  overflow: hidden;
  position: relative;
  //box-sizing: unset;
  background: {
    repeat: no-repeat;
    position: center bottom;
    size: cover;
  }

  .pop-content {
    position: relative;
    z-index: 4;
    min-height: 294px;
  }

  .pop-article-title,
  .pop-article-desc {
    text-shadow: 0 3px 6px rgba(#09170c, 0.24);
    color: $color-white;
  }

  @include media-breakpoint-up(lg) {
    .pop-content {
      min-height: 272px;
    }
  }

  @include media-breakpoint-down(sm) {
    .popular-bg-mb {
      @include bg-before();
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;

      background: {
        repeat: no-repeat;
        position: bottom center;
        size: cover;
      }
    }
  }
}

.popular-verse-of-day-bg,
.popular-article-bg,
.popular-verse-of-day,
.popular-article {
  .pop-article-title,
  .pop-article-desc,
  .pop-read-more {
    text-align: center;
  }
}
