.popular-categray-card {
  overflow-x: auto;

  @include media-breakpoint-down(lg) {
    &::after {
      content: "";
      display: block;
      width: 1px;
      min-width: 1px;
      height: 10px;
      visibility: hidden;
    }
  }

  @include media-breakpoint-down(xs) {
    padding-left: $gutter * 2;
  }
}

.categray-card {
  border-radius: $border-radius * 2;
  overflow: hidden;
  position: relative;
  min-width: 295px;
  width: 295px;
  height: 405px;
  margin-right: 23px;
  margin-bottom: $gutter;

  .card-img {
    height: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
  }

  .card-body {
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 64px 26px 24px 26px;
    background: transparent
      linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.6) 60%,
        rgba(0, 0, 0, 0.85098) 100%
      )
      0% 0% no-repeat padding-box;

    h2,
    p {
      color: $color-white;
    }

    h2 {
      font-size: 36px;
      margin-bottom: $gutter * 2;
      line-height: 1em;
    }

    p {
      font-size: 16px;
      @include limit-line(2, 1.33);
      height: 2 * 1.33em;
    }
  }

  @include media-breakpoint-up(xl) {
    &:nth-of-type(4) {
      margin-right: 0;
    }
  }

  @include media-breakpoint-down(mc) {
    width: 180px;
    min-width: 180px;
    height: 248px;
    margin-right: $gutter * 2;

    .card-body {
      padding: 28px $gutter * 2 $gutter * 2 $gutter * 2;

      h2 {
        font-size: 22px;
      }
      p {
        font-size: 14px;
      }
    }
  }
}

.sm-screen {
  @include media-breakpoint-down(sm) {
    max-width: auto;
    padding: {
      left: 0;
      right: 0;
    }
  }
}
