.list-card {
  @include flex();
  margin-bottom: $gutter * 3;

  .card-img {
    width: 38.38%;
    min-width: 38.37%;
  }

  .card-title {
    padding-left: $gutter * 2;
    width: 62.62%;

    p a {
      font-size: 16px;
      @include limit-line(3, 1.34);
    }

    .desc {
      font-size: 16px;
      margin-top: 12px;
      color: $color-normal;
      @include limit-line(3, 1.34);
    }
  }

  @include media-breakpoint-up(md) {
    margin-bottom: $gutter * 4;

    .card-img {
      min-width: 222px;
      width: 222px;
    }

    .card-title {
      padding-left: $gutter * 3;
      width: calc(100% - 222px);

      p a {
        font-size: 20px;
        font-weight: 700;
        @include limit-line(4, 1.34, null);
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .card-img {
      min-width: 250px;
      width: 250px;
    }

    .card-title {
      width: calc(100% - 250px);
    }
  }

  @include media-breakpoint-down(sm) {
    .card-title {
      .desc {
        display: none;
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .card-img {
      min-width: 110px;
      width: 110px;
    }

    .card-title {
      padding-left: 12px;
      width: calc(100% - 110px);
      margin-top: -3px;
    }
  }
}

.bible-item {
  margin-bottom: $gutter * 4;

  .bible-card {
    border-radius: $border-radius * 2;
    overflow: hidden;
    border: 1px solid $color-hr;
    box-shadow: 0 3px 10px rgba(#215329, 0.14);
    padding: $gutter * 3;
    padding-bottom: 60px;
    position: relative;
    transition: all 0.35s ease;

    &:hover {
      border-color: $color-footer-bg;
    }

    .line-see-more {
      position: absolute;
      bottom: $gutter * 3;
      right: 30px;
      z-index: 2;
      font-size: 16px;

      i {
        font-size: 20px;
      }
    }

    .bible-title {
      font-weight: 700;
      font-size: 20px;
    }

    .bible-text {
      font-size: 16px;
      margin-top: $gutter * 2;
      color: $color-normal;
      @include limit-line(5, 1.33);
    }
  }

  @include media-breakpoint-up(xl) {
    .bible-card {
      padding-bottom: 96px;

      .bible-text {
        margin-top: $gutter * 3;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .bible-card {
      height: 100%;
    }
  }

  @include media-breakpoint-down(mc) {
    margin-bottom: $gutter * 3;

    .bible-card {
      padding: {
        left: $gutter * 2;
        right: $gutter * 2;
        top: $gutter * 2;
      }
    }
  }
}
