.article-layout-6 {
  @include media-breakpoint-down(lg) {
    display: flex;
    flex-direction: row;
    margin-top: 24px;

    .article-card-cover {
      flex-shrink: 0;
      margin-right: 16px;
      width: 46%;
    }

    .article-card-body {
      border-bottom: none;
      padding-bottom: 0;
    }

    .article-card-title {
      margin-top: 0;
      font-size: 16px;
      line-height: 1.4em;
      @include limit-line(3, 1.4);
    }
  }
}

.article-box.layout-6 {
  .article-list {
    .article-card {
      margin-bottom: 0;

      .article-card-cover {
        display: none;
      }

      &:first-of-type {
        .article-card-cover {
          display: block;
        }

        margin-bottom: 16px;

        .article-card-body {
          border-bottom: none;
          padding-bottom: 0;
        }

        .article-card-title {
          font-size: 22px;
          line-height: 1.3em;
          color: #292929;
          font-weight: 700;
        }
      }

      &:last-of-type {
        .article-card-body {
          border-bottom: none;
        }
      }

      .article-card-title {
        @include limit-line(2, 1.33);
      }

      .article-card-body {
        border-bottom: 1px solid rgba(#000, 0.08);
        padding-bottom: 24px;
      }
    }

    @include media-breakpoint-down(lg) {
      .article-card {
        .article-card-cover {
          display: block;
        }

        &:first-of-type {
          margin-bottom: 0;

          .article-card-title {
            margin-top: 12px;
            font-size: 18px;
            font-weight: 500;
            @include limit-line(3, 1.33);
          }
        }
      }
    }

    .article-card {
      &:not(:first-of-type) {
        @extend .article-layout-6;
      }
    }

    @include media-breakpoint-down(md) {
      .article-card {
        &:nth-of-type(4) {
          display: none;
        }
      }
    }

    @include media-breakpoint-down(mc) {
      .article-card {
        .article-card-cover {
          display: none;
        }

        &:first-of-type {
          .article-card-title {
            font-size: 16px;
          }
        }

        .article-card-body {
          border-bottom: 1px solid rgba(#000, 0.08) !important;
          padding-bottom: 24px !important;
        }
        &:nth-last-of-type(2) {
          .article-card-body {
            border-bottom: none !important;
          }
        }
      }
    }
  }

  .layout-6-tablet {
    .left {
      .article-card {
        .article-card-body {
          position: absolute;
          bottom: 0;
          left: 12px;
          right: 12px;
          border-radius: 0 0 4px 4px;
          background: rgba(#1a1a1a, 0.5);
          padding: 8px 16px;

          .article-card-title {
            margin-top: 0;
            font-size: 16px;
            line-height: 1.4em;

            a {
              color: #fff;
            }
          }
        }
      }
    }

    .right {
      .article-card {
        margin-top: 20px;

        &:first-of-type {
          margin-top: 0;
        }

        @extend .article-layout-6;

        .article-card-cover {
          margin-right: 20px;
        }

        .article-card-body {
          .article-card-meta {
            .article-card-title {
              margin-top: 0;
            }
          }
        }
      }
    }
  }
}

.layout-6-tablet {
  & > div {
    margin-bottom: 72px;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  .tablet-title {
    margin-bottom: 40px;
    font-size: 32px;
    justify-content: center;
    display: flex;
    width: 100%;
    i {
      display: inline-block;
      transform: translateY(10px);
      user-select: none;
      pointer-events: none;
    }
  }
}

.article-card-section-title {
  font-size: 36px;
  line-height: 1.22em;
  text-align: center;
  margin-bottom: 48px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  i {
    display: none;
  }
  a {
    display: block;
  }

  @include media-breakpoint-down(md) {
    text-align: left;
    justify-content: flex-start;
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 32px;
    min-height: auto !important;
    margin-top: 48px;
    position: relative;
    padding-right: 24px;
    i {
      position: absolute;
      right: 0;
      top: 0;
      display: block;
      transform: translateY(4px);
    }
  }
}

.article-box.layout-6 {
  .article-list {
    &:first-of-type {
      .article-card-section-title {
        margin-top: 0;
      }
    }
  }
}
