$coverWidth: 0.3568;
$cardBodyWith: 1 - 0.3568;
.article-box.layout-8 {
  .article-card{
    margin-bottom: 24px;
  }
  @include media-breakpoint-up(md) {
    .article-card {
      display: flex;
      flex-direction: row;
    }

    .attrs {
      margin-top: 16px;
      display: inline-block;
    }

    .article-card-title {
      margin-top: 0;
      font-size: 22px;
      font-weight: 700;
      line-height: 1.2em;
    }

    .article-card-cover {
      flex-shrink: 0;
      overflow: hidden;
      width: #{$coverWidth * 100%};
    }

    .article-card-body {
      margin-left: 24px;
      flex: 1;
    }
  }

  @include media-breakpoint-down(sm) {
    .article-card-title {
      font-size: 16px;
      line-height: 1.4em;
    }
    .article-card-body {
      padding-right: 16px;
      &:nth-of-type(4){
        display: none;
      }
    }
    .article-card-description {
      display: none;
    }
    .attrs {
      display: none;
    }
  }

  @include media-breakpoint-down(mc) {
    .article-card-title{
      margin-top: 0;
    }
    .article-card {
      padding-bottom: 24px;
      border-bottom: 1px solid #ebebeb;
      margin-bottom: 24px;
      &:last-of-type{
        border-bottom: 0;
      }
    }
    .article-card-cover {
      display: none;
    }
    .article-card-body {
      padding-right: 0;
    }
  }
}
