#fix-player {
    position: fixed;
    left: 0;
    bottom: 20px;
    z-index: 10;
    display: flex;
    align-items: center;
    overflow: hidden;
  
    border-radius: 30px;
    padding: 12px 10px 12px 8px;
    max-width: 225px;
    height: 60px;
  
    background: #f9f9f9;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.11);
    transform: translateX(-100%);
    @include media-breakpoint-up(xl) {
      max-width: 236px;
    }
    .fp-left {
      display: flex;
      flex-direction: column;
      margin-left: 14px;
      margin-right: 18px;
      position: relative;
      &:after {
        content: "";
        width: 18px;
        position: absolute;
        top: 0;
        bottom: 0;
        background: -webkit-gradient(
          linear,
          left top,
          right top,
          from(#f9f9f9),
          to(rgba(249, 249, 249, 0))
        );
        background: linear-gradient(
          to right,
          #f9f9f9 0%,
          rgba(249, 249, 249, 0) 100%
        );
        left: -3px;
      }
      &:before {
        content: "";
        width: 18px;
        position: absolute;
        top: 0;
        bottom: 0;
        background: -webkit-gradient(
          linear,
          left top,
          right top,
          from(#f9f9f9),
          to(rgba(249, 249, 249, 0))
        );
        background: linear-gradient(
          to right,
          #f9f9f9 0%,
          rgba(249, 249, 249, 0) 100%
        );
        right: -4px;
        -webkit-transform: rotateZ(180deg);
        transform: rotateZ(180deg);
        z-index: 9;
      }
      .player-title {
        display: flex;
        width: 90px;
        overflow: hidden;
        position: relative;
        height: 19px;
        span {
          font-size: 14px;
          color: #4e535c;
          display: inline-block;
          position: absolute;
          line-height: 19px;
          left: 100%;
          top: 0px;
          white-space: nowrap;
          animation: today-scroll 30s linear infinite;
          margin-right: 0;
          transform: translateX(0);
          &:nth-last-of-type(1) {
            left: 100%;
            animation: today-scroll 30s linear 15s infinite;
            margin-right: 0;
          }
        }
      }
      .fp-data {
        margin-top: 2px;
        margin-left: 10px;
  
        font-size: 12px;
        color: #4e535c;
        line-height: 12px;
      }
    }
  
    .fp-center {
      border-radius: 50%;
      margin-right: 5px;
      width: 30px;
      height: 30px;
      background: linear-gradient(#57bc64, #2da140);
  
      .play-pause {
        margin-bottom: 0;
        padding: 0;
        width: 30px;
        height: 30px;
        i {
          color: #fff;
          font-size: 20px;
        }
        .icon-pause {
          display: none;
        }
        &.playing {
          .icon-play-arrow {
            display: none;
          }
          .icon-pause {
            display: block;
          }
        }
      }
      .icon-play-arrow-filled {
        width: 30px;
        height: 30px;
        position: relative;
        &:before {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }
      }
  
      .arrow-pause {
        position: relative;
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:after {
          position: absolute;
          content: "";
          width: 3px;
          height: 16px;
          display: block;
          background: #fff;
          left: 30%;
          border-radius: 8px;
          top: 11px;
        }
        &:before {
          position: absolute;
          content: "";
          width: 3px;
          height: 16px;
          display: block;
          background: #fff;
          right: 35%;
          border-radius: 8px;
          top: 11px;
        }
      }
    }
    .btn-icon-close{
      position: relative;
      i{
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        line-height: 44px;
      }
      .icon-close {
        font-size: 24px;
      }
      .icon-navigate-next{
        margin-right: -20px;
        font-size: 38px;
      }
    }     
  }
  
  
  
  .slide-left-PC {
    animation-name: slideLeftPC;
    animation-duration: 0.7s;	
    animation-fill-mode: both;
    animation-timing-function: ease;
  }
  .slide-left-MB {
    animation-name: slideLeftMB;
    animation-duration: 0.7s;	
    animation-fill-mode: both;
    animation-timing-function: ease;
  
  }
  
  @keyframes slideLeftPC {
    0% {
      transform-origin: 0 0;
      transform: translateX(0%);
    }
  
    100%{
      transform-origin: 0 0;
      transform: translateX(-100%);
    }
  }
  
  @keyframes slideLeftMB {
    0% {
      transform-origin: 0 0;
      transform: translateX(0%);
    }
  
    70% {
      transform-origin: 0 0;
      transform: translateX(-100%);
    }
  
    100%{
      transform-origin: 0 0;
      transform: translateX(calc(40px - 100%));
    }
  }
  
  .slide-right-PC {
    animation-name: slideRightPC;
    animation-duration: 0.7s;	
    animation-fill-mode: both;
    animation-timing-function: ease;
  }
  .slide-right-MB {
    animation-name: slideRightMB;
    animation-duration: 0.7s;	
    animation-fill-mode: both;
    animation-timing-function: ease;
  }
  
  @keyframes slideRightPC {
    0% {
      transform-origin: 0 0;
      transform: translateX(-100%);
    }
  
    100%{
      transform-origin: 0 0;
      transform: translateX(24px);
    }
  }
  
  @keyframes slideRightMB {
    0% {
      transform-origin: 0 0;
      transform: translateX(-100%);
    }
  
    100% {
      transform-origin: 0 0;
      transform: translateX(16px);
    }
  }