// Variables

// Color system
//  ===== 主色 =====
//  品牌色
$color-brand: #208f35;
//  品牌色-浅色
$color-light-brand: #36b049;

//  ===== 界面 =====
// Footer色背景色
$color-footer-bg: #2da140;
//  查看更多
$color-read-more: #e6edf5;
//  背景用色（上一篇、下一篇）
$color-list-atl: #f9f9fb;
//  卡片、Button背景底色
$color-card: #e7f5e8;

//  ===== 文字/icon =====
//  用于重要信息(版块标题、正文标题)
$color-title: #292929;
//  用于次要信息(如默认Tab标签，描述及摘要信息等)
$color-normal: #4c4c4c;
//  用于附属文字颜色
$color-sub: #7f7f7f;
//  文字hover 色
$color-text-hover: #36b049;
//  白色
$color-white: #ffffff;
//  数字序号
$color-number: #e0e0e0;

//  ===== 按钮 =====
//  用于Button的默认色、导航文字选中色
$color-button: #2da140;
// 用于普通类型按钮、文字 hover 色
$color-button-hover: #36b049;
//  用于button 背景色
$color-button-bg: #c5e7c7;

//  天蓝色
$color-sky-blue: #0084ff;

//  ===== 占位色 =====
$color-placeholder: #f4f4f4;

//  ===== 分割线 =====
//  分割线(首页文字标题)
$color-hr: #ebebeb;

//  ===== 辅色 =====

//  图片遮罩色(用在视频banner图上、tag图片上等等)
$color-image-mask: rgba(#000000, 0.25);
//  功能图标默认色
$color-icon: #51555c;
//  线性Button的颜色、链接文字颜色
$color-online: #ed7b18;

//  ===== 辅色 =====
//  正文内链色
$color-line-link: rgba($color-light-brand, 0.6);
//  搜索高亮颜色
$color-search-light: #c71717;
//  图库页面喜欢选中颜色
$color-red: rgba(#de491a, 0.86);

// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
  mc: 0,
  xs: 310px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1248px,
  ml: 1340px,
) !default;

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  sm: 556px,
  md: 744px,
  lg: 970px,
  xl: 1200px,
  ml: 1272px,
) !default;

// Grid columns
$gutter-md: 12px !default;
$gutter-sm: 8px !default;
//  最小槽宽值
$gutter: $gutter-sm;

// Components
//
// Define common padding and border radius sizes and more.
$line-height: 1.33 !default;
$heading-line-height-md: 1.33 !default;
$heading-line-height-sm: 1.2 !default;

$border-radius: 4px !default;

$font-family: "Lato", -apple-system, Avenir, Helvetica, Arial, sans-serif,
  Georgia !important;

//  Title、Text font size

//  Banner标题
$banner-title-xl: 48px;
$banner-title-md: 32px;
$banner-title-sm: 24px;

//  文章标题
$heading1-font-size-xl: 40px;
$heading1-font-size-md: 36px;
$heading1-font-size-sm: 30px;

//  板块标题(大标题、文章小标题)
$heading2-font-size-xl: 36px;
$heading2-font-size-md: 32px;
$heading2-font-size-sm: 24px;

//  标题(240手机端板块大标题、文章标题、列表页侧边栏标题)
$heading3-font-size-md: 22px;
$heading3-font-size-sm: 18px;

//  广告位标题
$commercial-title-font: 30px;

//  图片阅读页标题
$image-reader-title-md: 28px;
$image-reader-title-sm: 24px;

//  文字类文章标题1
$article1-title-md: 22px;
$article1-title-sm: 18px;

//  文字类文章标题2
$article2-title-md: 20px;
$article2-title-sm: 18px;

//  文章阅读页正文
$text1-font-size-md: 20px;
$text1-font-size-sm: 18px;

//  用于大多数文字(如文章、标题、正文、tab标签)
$text2-font-size-md: 18px;
$text2-font-size-sm: 16px; // 240屏幕

//  用附属信息文字(PC列表加载更多、文章分类、上传时间、观看次数等)
$text3-font-size: 16px;

//  用于辅助文字(如视频时长、页码、播放时长、footer署名信息等)
$caption-font-size: 14px;

//  基础字号
$font-size: 18px;
//  基础行高
$font-line-h: 1.33;

/*Flex function*/
@mixin flex(
  $align-item: null,
  $justify-content: null,
  $flex-wrap: null,
  $flex-direction: null,
  $align-content: null
) {
  display: flex;

  @if $align-item {
    align-items: $align-item;
  }

  @if $justify-content {
    justify-content: $justify-content;
  }

  @if $flex-wrap {
    flex-wrap: $flex-wrap;
  }

  @if $flex-direction {
    flex-direction: $flex-direction;
  }

  @if $align-content {
    align-content: $align-content;
  }
}

/*Limit line*/
@mixin limit-line($line: 3, $lineH: 1.4, $elip: "") {
  @if $elip== "" {
    display: block;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
  }

  -webkit-line-clamp: $line;
  max-height: calc(#{$lineH}em * #{$line});
  line-height: $lineH;
}

@mixin limit-line2($font-size: 16px, $line: 3, $lineH: 1.4, $elip: "") {
  $mh: $font-size * $line * $lineH;

  @if $elip== "" {
    display: block;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
  }

  -webkit-line-clamp: $line;
  max-height: $mh;
  line-height: $lineH;
}

@mixin limit-line-full-height($line: 3, $lineH: 1.4, $elip: "") {
  @if $elip== "" {
    display: block;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
  }

  -webkit-line-clamp: $line;
  height: calc(#{$lineH}em * #{$line});
}

// Breakpoint viewport sizes and media queries.

@function breakpoint-next(
  $name,
  $breakpoints: $grid-breakpoints,
  $breakpoint-names: map-keys($breakpoints)
) {
  $n: index($breakpoint-names, $name);
  @return if(
    $n !=null and $n < length($breakpoint-names),
    nth($breakpoint-names, $n + 1),
    null
  );
}

// Minimum breakpoint width. Null for the smallest (first) breakpoint.

@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {
  $min: map-get($breakpoints, $name);
  @return if($min !=0, $min, null);
}

// Maximum breakpoint width. Null for the largest (last) breakpoint.

@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {
  $next: breakpoint-next($name, $breakpoints);
  @return if($next, breakpoint-min($next, $breakpoints) - 0.02, null);
}

// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.

@function breakpoint-infix($name, $breakpoints: $grid-breakpoints) {
  @return if(breakpoint-min($name, $breakpoints) ==null, "", "-#{$name}");
}

// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.

@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);

  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media of at most the maximum breakpoint width. No query for the largest breakpoint.

@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {
  $max: breakpoint-max($name, $breakpoints);

  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media that spans multiple breakpoint widths.

@mixin media-breakpoint-between(
  $lower,
  $upper,
  $breakpoints: $grid-breakpoints
) {
  $min: breakpoint-min($lower, $breakpoints);
  $max: breakpoint-max($upper, $breakpoints);

  @if $min !=null and $max !=null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  } @else if $max==null {
    @include media-breakpoint-up($lower, $breakpoints) {
      @content;
    }
  } @else if $min==null {
    @include media-breakpoint-down($upper, $breakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and maximum widths.

@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  $max: breakpoint-max($name, $breakpoints);

  @if $min !=null and $max !=null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  } @else if $max==null {
    @include media-breakpoint-up($name, $breakpoints) {
      @content;
    }
  } @else if $min==null {
    @include media-breakpoint-down($name, $breakpoints) {
      @content;
    }
  }
}
