//  重置slick 切换箭头
.slick-arrow {
  opacity: 0;
  transition: all 0.3s ease;

  &.slick-prev,
  &.slick-next {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 20;
    font-size: 0;

    @include flex(center, center);
    width: 44px;
    height: 44px;
    background-color: $color-white;
    border-radius: 50%;
    transition: all 0.25s ease;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.16);

    font-family: "icomoon" !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;

    &::before {
      font-size: 32px;
      color: $color-icon;
    }
  }

  &.slick-prev {
    left: -8px;

    &::before {
      content: "\e923";
    }
  }

  &.slick-next {
    right: -8px;

    &::before {
      content: "\e926";
    }
  }

  &:hover {
    &::before {
      color: $color-light-brand;
    }

    &.slick-next {
      transform: translateX(4px);
    }

    &.slick-prev {
      transform: translateX(-4px);
    }
  }

  &.slick-disabled {
    display: none !important;
  }
}

.slick-dots {
  @include flex(center, center);

  li {
    margin: 0 4px;

    button {
      font-size: 0;
      padding: 0;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $color-number;
    }

    &.slick-active {
      button {
        width: 10px;
        height: 10px;
        border: 2px solid $color-brand;
        background-color: transparent;
      }
    }
  }
}
