.verses-by-topic {
  .title-item {
    border-bottom: 1px solid $color-hr;
    height: 100%;
    padding-top: $gutter * 3;
    padding-bottom: $gutter * 3;

    > a {
      text-decoration: none;
      align-items: flex-start;

      &:hover {
        .title {
          text-decoration: underline;
        }
      }
    }
    .num {
      font-size: 28px;
      color: $color-number;
      font-weight: 700;
      flex-shrink: 0;
      margin-right: $gutter * 2;
    }
    .title-content {
      min-height: 40px;

      .title {
        font-weight: 700;
        padding-right: $gutter;
        @include limit-line(2, 1.34);
      }
      .text-tips {
        display: inline-block;
        font-size: 14px;
        color: $color-sub;
        margin-top: $gutter * 2;
      }
    }
    .icon-navigate-next {
      font-size: 32px;
      margin-left: auto;
      color: $color-light-brand;
      flex-shrink: 0;
    }
  }
  @include media-breakpoint-up(md) {
    .title-item {
      padding-top: $gutter * 4;
      .title-content {
        .text-tips {
          margin-top: $gutter * 3;
        }
      }
    }
  }

  @include media-breakpoint-up(lg) {
    .col-12 {
      &:nth-of-type(n + 7) {
        .title-item {
          border: 0;
        }
      }

      &:nth-of-type(n + 10) {
        display: none;
      }
    }
  }
  @include media-breakpoint-up(xl) {
    > .row {
      margin: {
        left: -$gutter * 2;
        right: -$gutter * 2;
      }
      > .col-xl-4 {
        padding: {
          left: $gutter * 2;
          right: $gutter * 2;
        }
      }
    }

    .title-item {
      .num {
        font-size: 30px;
        margin-right: $gutter * 3;
      }
      .title-content {
        .title {
          font-size: 20px;
        }
      }
      > .icon-navigate-next {
        font-size: 32px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    .col-12 {
      &:nth-of-type(n + 5) {
        .title-item {
          border: 0;
        }
      }
      &:nth-of-type(n + 7) {
        display: none;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .col-12 {
      &:nth-of-type(n + 4) {
        .title-item {
          border: 0;
        }
      }
      &:nth-of-type(n + 5) {
        display: none;
      }
    }
  }
}
