.article-card-cover,
.video-box .rich-slick-card .cover {
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 4px;

  a {
    display: block;
    padding-bottom: 62.25%;
    overflow: hidden;
  }

  img {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;

    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
    transition: transform .3s;
    transform: translate3D(-50%, -50%, 0);
    backface-visibility: hidden;
  }
}

.article-card-cover{
  @include media-breakpoint-up(xl) {
    &:hover {
      img {
        transform: translate3D(-50%, -50%, 0) scale(1.08);
      }
    }
  }
}

.article-box,
.article-box.layout-7 {
  margin-bottom: -24px;

  .article-card {
    margin-bottom: 32px;
    @include media-breakpoint-down(sm) {
      margin-bottom: 24px;
    }
  }

  .attrs {
    margin-top: 16px;
    display: inline-block;
    .hide {
      opacity: 0;
      pointer-events: none;
      user-select: none;
    }
  }

  .article-card-title {
    font-size: 18px;
    line-height: 1.33em;
    color: #292929;
    margin-top: 16px;
    @include limit-line(3, 1.33);
    @media (max-width: 241px) {
      font-size: 16px;
      font-weight: 400;
    }
  }

  .article-card-description {
    margin-top: 16px;
    font-size: 16px;
    font-weight: 400;
    color: #4c4c4c;
    line-height: 1.4em;
  }
}

.article-box.default {
  @include media-breakpoint-between(lg, lg) {
    .article-card.col-lg-4 {
      &:nth-last-of-type(1) {
        display: none;
      }
    }
  }
  @include media-breakpoint-down(sm) {
    .article-card.col-12 {
      &:nth-of-type(4) {
        display: none;
      }
    }
  }
}
