@import "./banner/banner-1.scss";
@import "./banner/banner-2.scss";
@import "./banner/banner-3.scss";
@import "./banner/banner-4.scss";
@import "./banner/banner-5.scss";

#banner {
  width: 100%;
  position: relative;
  overflow: hidden;

  div[class^="banner-carousel"] {
    .ban-item {
      &:not(:first-child) {
        display: none;
      }
      &.slick-slide {
        display: block;
      }
    }
  }

  .ban-card {
    background-color: $color-placeholder;
    position: relative;

    img {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;

      &.loaded {
        opacity: 1;
      }
    }
  }

  .ban-item {
    .card-title {
      width: 100%;

      @include flex(center, center);
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 2;

      background: transparent
        linear-gradient(
          180deg,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.6) 60%,
          rgba(0, 0, 0, 0.85098) 100%
        )
        0% 0% no-repeat padding-box;
      padding: 24px $gutter * 2 28px $gutter * 2;

      p {
        font-weight: 700;
        color: $color-white;
      }
    }
  }

  .banner-carousel-2,
  .banner-carousel-3,
  .banner-carousel-5,
  .large-ban {
    .ban-card {
      width: 100%;
      padding-top: 56.25%;
      overflow: hidden;

      img {
        width: 100%;
        transform: scale(1);
        transition: transform 0.8s ease, opacity 2s ease-in-out;
      }

      &:hover {
        img {
          transform: scale(1.05);
        }
      }
    }
  }

  .banner-carousel-3,
  .banner-carousel-5,
  .large-ban {
    @include media-breakpoint-down(sm) {
      border-radius: $gutter;
      overflow: hidden;
      -webkit-backface-visibility: hidden;
      -moz-backface-visibility: hidden;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
    }

    @include media-breakpoint-up(md) {
      .ban-item {
        .ban-card {
          border-radius: $gutter;
          overflow: hidden;
          -webkit-backface-visibility: hidden;
          -moz-backface-visibility: hidden;
          -webkit-transform: translate3d(0, 0, 0);
          -moz-transform: translate3d(0, 0, 0);
        }
      }
    }
  }
}

//  进度条动画

#banner {
  .slick-dots .slick-active button,
  .swiper-pagination .swiper-pagination-bullet-active {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 3px;
      animation-name: slider-progress;
      animation-duration: 5.5s;
      background-color: $color-white;
      animation-fill-mode: forwards;
    }
  }
}

[class*="banner-carousel"] {
  &:hover {
    .slick-dots .slick-active button,
    .swiper-pagination .swiper-pagination-bullet-active {
      &::after {
        animation-play-state: paused;
      }
    }
  }
}

@keyframes slider-progress {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
