.sidebar-box {
  .rich-slick-card {
    .cover::after {
      background-size: contain;
    }
  }
  .article-box.layout-4 {
    .article-list {
      .article-card {
        margin-bottom: 24px;
        &:last-of-type {
          .article-card-title {
            margin-bottom: 0 !important;
          }
        }
      }
      .article-card-body {
        padding-left: 40px;
        @include media-breakpoint-down(mc) {
          padding-left: 0;
        }
        .article-card-title {
          font-size: 18px;
          margin-bottom: 32px;
          @include limit-line(2, 1.33);

          @include media-breakpoint-between(lg, lg) {
            margin-bottom: 24px;
          }
        }
        .article-card-meta {
          &::before {
            font-size: 22px;
            top: 50%;
            transform: translateY(-50%);
            left: -40px;
          }
        }
      }

      .article-card {
        &:last-of-type {
          .article-card-body {
            border-bottom: none !important;
          }
        }
        @include media-breakpoint-between(lg, lg) {
          margin-bottom: 24px;
        }
      }

      @include media-breakpoint-between(md, md) {
        .article-card {
          &:nth-last-of-type(2) {
            .article-card-body {
              border-bottom: none !important;
            }
          }
        }
      }

      @include media-breakpoint-down(sm) {
        .article-card-body {
          .article-card-title {
            font-size: 16px;
            margin-bottom: 24px;
          }
        }
      }
    }
  }
}

.sidebar-box {
  .default-title,
  .sidebar-title-2 {
    font-size: 22px;
    line-height: 1.2em;
    margin-bottom: 20px;
  }

  .sidebar-title-2 {
    margin-top: 40px;
  }

  @include media-breakpoint-down(md) {
    .default-title {
      margin-top: 32px;
    }
  }
  @include media-breakpoint-down(sm) {
    .default-title,
    .sidebar-title-2 {
      margin-top: 24px;
    }
  }
}

.sidebar-box {
  .article-box {
    margin-bottom: 0;
    .article-list {
      .article-card {
        &:nth-last-child(1) {
          margin-bottom: 0;
        }
      }
    }
  }
}
