#banner {
  .banner-carousel-2 {
    border-radius: $gutter;
    overflow: hidden;

    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);

    .ban-card {
      .card-title {
        p {
          font-size: 16px;
          line-height: 1.5;
          @include limit-line(2, 1.5);
        }
      }
    }

    @include media-breakpoint-up(md) {
      .ban-card {
        .card-title {
          height: 140px;
          padding: {
            left: $gutter * 3;
            right: $gutter * 3;
          }

          p {
            font-size: 24px;
          }
        }
      }
    }

    @include media-breakpoint-down(sm) {
      .ban-card {
        .card-title {
          p {
            line-height: 1.2;
            @include limit-line(2, 1.2, null);
          }
        }
      }
    }

    @include media-breakpoint-down(mc) {
      .ban-card {
        .card-title {
          padding-bottom: 22px;
          p {
            font-size: 14px;
          }
        }
      }
    }
  }
}
