.popular-article {
  padding-top: 44px;
  padding-bottom: $gutter * 6;

  .pop-content-text {
    h4,
    p {
      text-align: center;
    }

    .pop-article-title {
      font-size: 24px;
      font-weight: 700;
    }

    .pop-article-desc {
      font-size: 16px;
      margin-top: $gutter * 3;
      @include limit-line(2, 1.5);
    }
  }

  .tips {
    display: inline-block;
    position: relative;
    padding-bottom: 4px;
    margin-bottom: $gutter * 4;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      display: block;
      width: 90%;
      height: 2px;
      background-color: $color-brand;
    }

    &:hover {
      &::after {
        animation-name: tips-animate;
        animation-duration: 0.8s;
        animation-fill-mode: both;
      }
    }
  }

  .pop-read-more {
    margin-top: $gutter * 6;
  }

  @include media-breakpoint-up(md) {
    padding-bottom: $gutter * 8;

    .tips {
      font-size: 20px;
    }

    .pop-read-more {
      margin-top: $gutter * 5;
    }

    .tips {
      margin-bottom: $gutter * 5;
    }
  }

  @include media-breakpoint-down(mc) {
    .pop-content {
      .pop-content-text {
        .pop-article-title {
          font-size: 18px;
        }
      }

      .tips {
        margin-bottom: $gutter * 3;
      }
    }

    .pop-read-more {
      font-size: 12px;
      margin-top: $gutter * 3;
    }
  }
}

.popular-article-bg {
  @include bg-before();
  padding: 56px $gutter * 2 70px $gutter * 2;
  background-position: left bottom;

  .pop-article-title {
    font-size: 24px;
  }

  .pop-article-desc {
    font-size: 16px;
    margin-top: $gutter * 3;

    @include limit-line(2, 1.5);
  }

  .pop-content-text {
    margin-top: $gutter * 2;
  }

  .pop-read-more {
    margin-top: $gutter * 6;
  }

  @include media-breakpoint-up(md) {
    padding: 72px 72px 76px 72px;

    .pop-article-title {
      font-size: 36px;
    }

    .pop-article-desc {
      font-size: 18px;
    }

    .pop-read-more {
      margin-top: $gutter * 5;
    }
  }

  @include media-breakpoint-up(xl) {
    .pop-article-title {
      font-size: 40px;
    }

    .pop-article-desc {
      font-size: 20px;
    }
  }

  @include media-breakpoint-down(sm) {
    &.worship {
      padding-bottom: 84%;

      .pop-content {
        min-height: auto;
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .pop-article-title {
      font-size: 20px;
    }

    .pop-article-desc {
      font-size: 14px;
    }

    &.worship {
      padding-bottom: 120vw;
    }
  }
}

@keyframes tips-animate {
  0% {
    transform: scaleX(1);
    transform-origin: right;
  }

  30% {
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.15s ease-in-out;
  }

  70% {
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.25s ease-in-out;
  }

  100% {
    transform: scaleX(1);
    transform-origin: left;
  }
}
