#banner {
  .f-p-first-plate {
    .card-title {
      justify-content: space-between !important;

      p {
        margin-right: auto;
        margin-left: $gutter;
        font-size: 16px;
        line-height: 1.4;
        @include limit-line(2, 1.4);
      }

      i,
      .watch {
        color: $color-white;
        flex-shrink: 0;
      }

      i {
        font-size: 24px;
      }

      .watch {
        background-color: rgba($color-button, 0.88);
        border-radius: $border-radius;
        margin: 0 4px 0 $gutter * 2;
        transition: all 0.35s ease;

        &:hover {
          background-color: $color-button-hover;
        }
      }
    }

    .large-ban {
      .card-title {
        padding: {
          top: 12px;
          bottom: 12px;
        }
      }
    }

    .col-md-8 {
      padding-right: 4px;
    }

    .col-md-4 {
      padding-left: 4px;

      .ban-item {
        .card-title {
          padding-bottom: $gutter;
          padding-left: 10px;
        }
      }
    }
  }

  @include media-breakpoint-up(md) {
    .banner-carousel-3 {
      .ban-item {
        display: block !important;
        &:nth-of-type(2) {
          margin-bottom: 8px;
        }

        &.md-hide {
          display: none !important;
        }
      }
    }

    .f-p-first-plate {
      .watch {
        font-size: 12px;
        padding: 4px 12px;
      }
    }
  }

  @include media-breakpoint-up(lg) {
    .f-p-first-plate {
      .card-title {
        p {
          font-size: 18px;
          margin-left: $gutter * 2;
        }
        .watch {
          font-size: 16px;
          padding: $gutter 32px;
          margin: 0 18px 0 42px;
        }

        i {
          font-size: 32px;
        }
      }

      .large-ban {
        .card-title {
          padding: {
            top: 24px;
            bottom: 16px;
          }
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .f-p-first-plate {
      .card-title {
        p {
          font-size: 24px;
        }
        .watch {
          margin-left: 50px;
        }
      }

      .large-ban {
        .card-title {
          padding: {
            top: 30px;
            bottom: 18px;
          }
        }
      }
    }
  }
}
