$sub-bg: #e7f5e8;
$color-alert-danger: $color-search-light;
.module-deck {
  background-color: #f8fcf8;
  padding-bottom: $gutter * 6;
  overflow: hidden;
  border-radius: $border-radius * 2;

  .sub-gard {
    padding: {
      left: $gutter * 2;
      right: $gutter * 2;
    }
  }
  .sub-conduct {
    width: 100%;
    height: 156px;
    position: relative;
    background: {
      repeat: no-repeat;
      size: auto 90%;
      color: $sub-bg;
      position: bottom center;
    }

    &::after {
      content: "";
      width: 20px;
      height: 20px;
      background-color: $sub-bg;
      transform: rotate(45deg);
      position: absolute;
      bottom: -10px;
      left: 0;
      right: 0;
      margin: auto;
    }
  }

  .checkbox + span {
    &::after {
      top: 0;
    }

    &::before {
      top: 2px;
    }
  }
}

.subscribe-wrapper {
  position: relative;
  height: auto;
  transition: height 0.35s ease-out;
  overflow: hidden;
  margin-top: $gutter * 5 + 2px;

  &.module-deck {
    padding: 0;
  }

  .free-subscribe,
  .email-subscribe {
    h4 {
      font-size: 22px;
      margin-bottom: $gutter * 2;
      text-align: center;
    }
  }

  .news-privacy {
    span,
    a {
      font-size: 14px;
    }

    a {
      color: $color-button;
    }
  }

  .free-subscribe {
    transition: all 0.45s ease-in-out;

    .news-desc {
      display: inline;
      font-size: 16px;
    }

    .more {
      font-size: 14px;
      color: $color-light-brand;
      text-decoration: unset;
      padding-left: 6px;
      &:hover {
        text-decoration: underline;
      }
    }

    .sub-list {
      text-align: center;
      margin-top: 4px;
      .btn {
        margin-top: $gutter * 2;
        width: 256px;
        font-size: 14px;
      }
    }

    .news-privacy {
      margin-top: $gutter * 2;
      margin-bottom: $gutter;
    }
  }

  .email-subscribe {
    background-color: #f8fcf8;
    min-height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4;

    transition: all 0.35s ease-in-out;

    h4 {
      font-weight: 500;
    }

    .sub-list {
      display: flex;
      flex-wrap: wrap;

      label {
        margin-bottom: $gutter;
        width: 50%;
      }

      .checkbox + span {
        &::after {
          border-color: $color-light-brand;
        }
      }

      span {
        font-size: 14px;
      }
    }

    .sub-email {
      display: flex;
      margin-top: $gutter * 2;
      height: 42px;
      overflow: hidden;

      label,
      br {
        display: none;
      }

      input {
        border: 0;

        &[type="submit"] {
          background-color: $color-button-hover;
          color: $color-white;
          font-size: 16px;
          position: relative;
          z-index: 2;
          margin-left: -10px;

          padding: {
            left: 18px;
            right: 22px;
          }

          border-radius: $gutter;

          &:hover {
            background-color: rgba($color-button-hover, 0.85);
          }
        }

        &[type="email"] {
          width: calc(100% - 92px);
          padding-left: $gutter * 2;
          border: 1px solid rgba(#000000, 0.1);
          border-top-left-radius: $gutter;
          border-bottom-left-radius: $gutter;
          outline: none;
          font-size: 16px;
        }
      }
    }

    .news-privacy {
      margin-top: $gutter;

      a {
        color: $color-brand;
      }

      input[type="checkbox"] {
        width: 16px;
        height: 16px;
        margin-right: 4px;
        transform: translateY(2px);
      }
    }

    .back-options {
      margin: auto;
      display: block;
      margin-top: $gutter * 3;
      background-color: transparent;
      color: $color-brand;
      font-size: 16px;

      i {
        display: inline-block;
        transform: translateY(5px);
      }
    }
  }

  .mc4wp-response {
    margin-top: $gutter + 2px;
  }
}

.sub-event {
  .email-subscribe {
    opacity: 0;
    transform: translateX(100%);
  }

  &.change {
    .free-subscribe {
      transform: translateX(-30%);
    }

    .email-subscribe {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

.news-privacy {
  &.warning {
    animation: shakes 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;

    span,
    a {
      color: $color-alert-danger !important;
    }

    span {
      &::after {
        border-color: $color-alert-danger;
      }
    }
  }
}

@keyframes shakes {
  10%,
  90% {
    transform: translate3d(0, -1px, 0);
  }

  20%,
  80% {
    transform: translate3d(0, 2px, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(0, -4px, 0);
  }

  40%,
  60% {
    transform: translate3d(0, 4px, 0);
  }
}
