.share-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1200;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  .share-panel {
    background-color: $color-white;
    padding: $gutter + 6px $gutter * 2;
    border-top-left-radius: $border-radius;
    border-top-right-radius: $border-radius;
    position: absolute;
    z-index: 1010;
    box-shadow: 0 3px 16px rgba(#000000, 0.16);
    transition: all 625ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    opacity: 0;
  }

  .share-content {
    h4 {
      font-size: 18px;
    }

    .share-target {
      margin-top: 36px;
    }

    .a2a_kit {
      @include flex(null, flex-start, nowrap);

      a[class^="a2a_"] {
        width: 56px;
        min-width: 56px;
        height: 56px;
        padding: 4px;
        display:block;
        margin-right: $gutter * 3;
        margin-bottom: $gutter * 2;

        &.a2a_dd{
          margin-right: 0 !important;
        }
      }
    }
  }

  .share-wrapper-mask {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(#000, 0.6);
    z-index: 999;
    transition: opacity 425ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    will-change: opacity;
    opacity: 0;
  }

  .copy-link {
    margin-top: $gutter * 2;
  }

  .copy-link-renderer {
    background: #f5f7fa;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    @include flex(center, flex-start);

    i {
      padding: 0 8px;
      font-size: 22px;
      color: #919499;
    }

    input {
      width: 100%;
      background: transparent;
      border: none;
      border-right: 1px solid rgba(0, 0, 0, 0.1);
      margin: 10px 0;
      padding-right: 16px;
      color: #919499;
    }

    .btn-copy {
      white-space: nowrap;
      background-color: transparent;
      padding: 0 $gutter * 2;
      color: $color-brand;
    }
  }

  .start-at {
    @include flex(center, flex-start);

    span {
      color: $color-sub;
      font-size: 14px;
      padding-left: 26px;
    }

    .start-at-timestamp {
      border-radius: 4px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      padding: 4px 8px;
      width: 84px;
      color: #262b33;
      font-family: Roboto;
      margin-left: 20px;
    }

    .checkbox+span::after {
      border-color: #3998e6;
      top: 0;
    }

    .checkbox+span::before {
      top: 2px;
    }
  }

  .start-at {
    margin-top: $gutter * 3;
  }

  .close-btn {
    background-color: transparent;
    position: absolute;
  }

  &.shows {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;

    .share-wrapper-mask,
    .share-panel {
      opacity: 1;
    }
  }

  &.hides {
    transition: all 0.1s ease 0.65s;
  }

  @include media-breakpoint-up(sm) {
    .share-panel {}
  }

  @include media-breakpoint-up(md) {
    .share-panel {
      padding: $gutter * 3 $gutter * 2;
    }
  }

  @include media-breakpoint-up(xl) {
    @include flex(center, center);

    &.present {
      position: absolute;

      .share-wrapper-mask {
        display: none;
      }
    }

    .share-panel {
      position: relative;
      border-radius: $border-radius;
      padding: $gutter * 4 $gutter * 3;
      width: 560px;
      min-height: 260px;
    }

    .share-content {
      .a2a_kit {
        flex-wrap: wrap;
      }
    }

    .close-btn {
      width: 44px;
      height: 44px;
      @include flex(center, center);
      right: 10px;
      top: 16px;

      span {
        display: none;
      }

      i {
        font-size: 28px;
      }
    }
  }

  @include media-breakpoint-down(lg) {
    .share-panel {
      padding-bottom: 64px;
      bottom: 0;
      left: 0;
      right: 0;
      width: 70%;
      min-height: 250px;
      margin: auto;
      will-change: transform;
      transform: translateY(120%);
      opacity: 0;
      transition: all 425ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

      .share-target {
        overflow-x: auto;
      }
    }

    .start-at {
      margin-bottom: 34px;
    }

    .close-btn {
      bottom: 12px;
      left: 0;
      right: 0;
      margin: auto;
      height: 52px;
      width: 70%;

      i {
        display: none;
      }

      span {
        color: $color-brand;
      }
    }

    &.shows {
      .share-panel {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }

  @include media-breakpoint-down(xs) {
    .share-panel {
      width: 100%;
    }
  }
}