.popular-verse-of-day {
  padding: {
    top: 40px;
    bottom: 72px;
  }
  .pop-article-title {
    font-size: 24px;
  }

  .pop-content-text {
    margin-top: $gutter * 3;
    .pop-article-desc {
      font-size: 22px;
      line-height: 1.6;
      font-weight: 700;
    }
  }

  .pop-read-more {
    margin-top: 32px;
  }

  @include media-breakpoint-up(md) {
    padding: {
      top: 42px;
      bottom: 42px;
    }

    .pop-article-title {
      font-size: 32px;
    }
  }

  @include media-breakpoint-up(xl) {
    padding: {
      top: 44px;
      bottom: 62px;
    }

    .pop-article-title {
      font-size: 36px;
    }
  }
}

.popular-verse-of-day-bg {
  padding: 40px 20px 50px 20px;

  .pop-article-title {
    font-size: 24px;
  }

  .pop-article-desc {
    font-size: 22px;
  }

  .pop-content-text {
    margin-top: $gutter * 2;
  }

  .pop-read-more {
    margin-top: $gutter * 3;
  }

  .timer {
    color: $color-white;
  }

  @include media-breakpoint-up(md) {
    padding: 60px 70px;
    @include bg-before();

    .pop-article-title {
      font-size: 40px;
    }

    .pop-article-desc {
      font-size: 24px;
    }
  }

  @include media-breakpoint-up(xl) {
    padding: 76px 240px 60px 240px;
  }
}

.popular-verse-of-day,
.popular-verse-of-day-bg {
  .timer {
    font-size: 16px;
    margin-top: 24px;
    box-shadow: inset 0 -0.55rem 0 rgba(32, 143, 53, 0.54);
  }

  @include media-breakpoint-up(md) {
    .timer {
      font-size: 20px;
    }
  }

  @include media-breakpoint-down(mc) {
    .pop-article-title {
      font-size: 22px;
    }

    .pop-article-desc {
      font-size: 20px !important;
    }

    .pop-read-more {
      margin-top: 20px;
    }

    .timer {
      margin-top: $gutter * 2;
    }
  }
}
