#banner {
  .banner-carousel-1 {
    .ban-item {
      width: 100%;
      position: relative;
      overflow: hidden;

      .card-title {
        p {
          line-height: 1.2;
          @include limit-line(2, 1.2);
        }
      }
    }

    .ban-card img {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  @include media-breakpoint-up(md) {
    .banner-carousel-1 {
      .ban-item {
        .ban-card {
          height: 300px;
        }

        .card-title {
          height: 116px;
          p {
            max-width: 658px;
            font-size: 22px;
          }
        }

        img {
          height: 100%;
          max-width: none;
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    max-width: 1920px;

    .banner-carousel-1 {
      .ban-item {
        .ban-card {
          padding-top: 29.34%;
          height: auto;
        }

        .card-title {
          height: 190px;

          p {
            font-size: 28px;
            max-width: 1060px;
          }
        }
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .ban-item {
      .ban-card {
        padding-top: 56%;
      }
    }
  }

  .slick-dots {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

    li {
      padding-bottom: 8px;
      button {
        width: 48px;
        height: 3px;
        border-radius: 3px;
        background-color: rgba(255, 255, 255, 0.4);
        border: 0;
      }

      // &.slick-active button {
      //   background-color: $color-white;
      // }
    }
  }

  @include media-breakpoint-down(mc) {
    .slick-dots {
      li button {
        width: 38px;
      }
    }
  }
}
