.audio-player {
  display: flex;
  justify-content: space-between;

  border-radius: 28px;
  padding: 6px 10px;
  margin-bottom: $gutter * 5;
  width: auto;
  height: 56px;

  background-color: $color-placeholder;
  .play-pause {
    i {
      font-size: 28px;
    }
    .icon-pause {
      display: none;
    }
    &.playing {
      .icon-play-arrow {
        display: none;
      }
      .icon-pause {
        display: block;
      }
    }
  }

  .range-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 12px;
    width: calc(100% - 44px);
    @include media-breakpoint-up(md) {
      padding-right: 0;
      width: calc(100% - 272px);
    }
    #range-target {
      height: 4px;
      width: calc(100% - 90px);
    }
  }

  .volume-box {
    width: 144px;
    #volume-range-target {
      height: 4px;
      width: calc(100% - 90px);
    }
  }

  .current-time,
  .duration {
    display: inline-block;
    font-size: 12px;
  }
}
@-webkit-keyframes today-scroll {
  0% {
    -webkit-transform: translate(0%);
  }
  100% {
    -webkit-transform: translate(-210%);
  }
}

@keyframes today-scroll {
  0% {
    transform: translate(0%);
  }
  100% {
    transform: translate(-210%);
  }
}

//  TODO 播放器加载时兜底占位图效果

.audio-player.loading {
  .play-pause {
    i {
      width: 28px;
      height: 28px;
      color: rgba(#000, 0);
      border-radius: 50%;
      background-color: rgba(#000, 0.1);
    }
    i.icon-play-arrow{
      display: block;
    }
  }
  .range-box {
    span {
      border-radius: 2px;
      color: rgba(#000, 0);
      background-image: repeating-linear-gradient(
        180deg,
        #f4f4f4,
        #f4f4f4 4px,
        rgba(#000, 0.1) 4px,
        rgba(#000, 0.1) 10px,
        #f4f4f4 10px,
        #f4f4f4 100%
      );
    }
    #range-target {
      height: 6px;
      background-color: rgba(#000, 0.1);
    }
  }
  .volume-box {
    i {
      display: block;
      width: 28px;
      height: 28px;
      color: rgba(#000, 0);
      border-radius: 50%;
      background-color: rgba(#000, 0.1);
    }
    #volume-range-target {
      height: 6px;
      background-color: rgba(#000, 0.1);
    }
  }

  .icon-list-download {
    display: block;
      width: 28px;
      height: 28px;
    color: rgba(#000, 0);
    border-radius: 50%;
    background-color: rgba(#000, 0.1);
  }
}
