@mixin module {
  background-color: $color-white;
  box-shadow: 0 2px 8px rgba(#000, 0.16);
  padding-bottom: $gutter * 6;
  overflow: hidden;
  border-bottom-left-radius: $border-radius * 2;
  border-bottom-right-radius: $border-radius * 2;
}

.topics-item {
  .card-img {
    > a {
      border-radius: $border-radius * 2;
    }

    img {
      transition: transform 0.8s ease, opacity 2s ease-in-out;
      transform: scale(1);
    }
  }

  &:hover {
    img {
      transform: scale(1.05);
    }
  }

  .card-title {
    padding-top: $gutter * 2;

    h3 {
      font-weight: 700;
      @include limit-line(2, 1.3);
      height: calc(1.3em * 2);
    }
  }

  .card-title,
  .car-desc {
    width: 100%;
  }

  .car-desc {
    font-size: 16px;
    margin-top: 16px;
    color: $color-normal;
    @include limit-line(3, 1.4);
    height: calc(3 * 1.4em);
  }

  .read-more {
    margin-top: $gutter * 4;
    padding: 9px 26px;
  }

  .topics-content {
    display: flex;
    flex-wrap: wrap;
  }

  @include media-breakpoint-up(xs) {
    .card-title {
      h3 {
        font-weight: 700;
        font-size: 22px;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .car-desc {
      @include limit-line(2, 1.4, null);
      height: calc(2 * 1.4em);
    }
  }

  @include media-breakpoint-down(lg) {
    @include module;
    .topics-card {
      .card-img {
        a {
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
        }
      }
    }

    .topics-content {
      padding: 0 $gutter * 2;
      justify-content: center;
    }
  }

  @include media-breakpoint-down(sm) {
    .topics-content {
      padding: 0 12px;
    }
  }

  @include media-breakpoint-down(mc) {
    padding-bottom: $gutter * 3;

    .topics-content {
      .car-desc {
        display: none !important;
      }
      .card-title {
        h3 {
          @include limit-line(4, 1.3, null);
          height: calc(1.3em * 4);
        }
      }
    }

    .read-more {
      margin-top: $gutter * 3;
      padding: 6px 22px;
    }
  }
}

.white-module {
  @include module;

  @include media-breakpoint-up(xl) {
    .topics-content {
      padding: 0 $gutter * 2;
      justify-content: center;
    }

    .topics-card {
      .card-img {
        a {
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
        }
      }
    }
  }
}

.topics-carousel {
  @include media-breakpoint-down(sm) {
    .col-12 {
      max-width: 548px;
      flex: auto;

      &:nth-of-type(n + 2) {
        display: none;
      }

      &.slick-slide {
        display: block;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .slick-track {
      width: 100% !important;
      transform: translate3d(0px, 0px, 0px) !important;
    }
    .slick-dots {
      display: none;
    }
  }
}
