.article-box.layout-4,
.article-box.layout-5 {
  .article-list {
    counter-reset: listNumber;

    .article-card-body {
      padding-left: 48px;
      position: relative;
      border-bottom: 1px solid rgba(#000, 0.08);
      height: 100%;
      .article-card-meta {
        position: relative;
        &::before {
          counter-increment: listNumber;
          content: "0" counters(listNumber, "");
          font-weight: 700;
          font-size: 30px;
          color: #303030;
          line-height: 1.2em;
          font-family: "Frank Ruhl Libre";
          opacity: 0.16;
          position: absolute;
          left: -50px;
          top: 0;
        }
      }

      .article-card-title {
        margin-top: 0;
        font-size: 20px;
        font-weight: 500;
        color: #292929;
        line-height: 1.33em;
        margin-bottom: 16px;
        @include media-breakpoint-up(xl) {
          @include limit-line-full-height(2, 1.33);
        }
        @include media-breakpoint-between(md, md) {
          @include limit-line-full-height(3, 1.33);
        }
        @include media-breakpoint-between(lg, lg) {
          @include limit-line-full-height(2, 1.33);
        }
      }
    }

    .attrs {
      margin-bottom: 40px;
      display: block;
    }

    @include media-breakpoint-between(lg, lg) {
      .article-card-body {
        .article-card-title {
          margin-bottom: 32px;
        }
      }
      .article-card {
        margin-bottom: 32px;
      }
    }
    @include media-breakpoint-down(sm) {
      .article-card-body {
        padding-left: 42px;
        .article-card-title {
          font-size: 18px;
          @include limit-line(3, 1.33);
        }
        .article-card-meta {
          &::before {
            font-size: 28px;
            left: -42px;
          }
        }
      }
      .attrs {
        margin-bottom: 24px;
      }
    }

    @include media-breakpoint-down(mc){
      .article-card {
        margin-bottom: 24px;
      }
      .article-card-body {
        padding-left: 0;

        .article-card-meta {
          &::before {
            display: none;
          }
        }

        .article-card-title {
          font-size: 16px;
          line-height: 1.4em;
          margin-bottom: 24px;
        }

        .attrs {
          display: none;
        }
      }
    }
  }
}

.article-box.layout-4 {
  .article-list {
    .article-card {
      //  调显示的个数
      &:nth-of-type(n + 4) {
        display: none;
      }
      @include media-breakpoint-up(md) {
        &:nth-of-type(n + 4) {
          display: block;
        }
      }

      //  调最后一行的下边框
      &:nth-of-type(3) {
        .article-card-body {
          border: none;
        }
      }
      @include media-breakpoint-up(sm) {
        &:nth-of-type(3) {
          .article-card-body {
            border-bottom: 1px solid rgba(#000, 0.08);
          }
        }
        &:nth-of-type(n + 5) {
          .article-card-body {
            border-bottom: none;
          }
        }
      }
      @include media-breakpoint-up(xl) {
        &:nth-of-type(n + 4) {
          .article-card-body {
            border: none;
          }
        }
      }
    }
  }
}
