.banner-with-popular {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  .left-box {
    width: 65.86%;
    max-width: 822px;
  }
  .right-box {
    width: 32.05%;
    max-width: 400px;
    margin-bottom: -24px;
  }
  .image-set {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    .left {
      width: 68.49%;
      max-width: 563px;
      .card-title a {
        font-size: 24px;
      }
    }
    .right {
      width: 28.58%;
      max-width: 235px;
      .card-title a {
        line-height: 1.33em;
        @include limit-line(2, 1.33);
      }
      .article-card {
        margin-bottom: 24px;
      }
    }
  }

  .right-box {
    .list-title {
      font-size: 22px;
      line-height: 1.33em;
      margin-bottom: 20px;
    }
    .article-box {
      &.layout-5 {
        .article-list {
          .article-card {
            @include media-breakpoint-up(xl) {
              &:last-of-type {
                display: none;
              }
              &:nth-last-of-type(2) {
                .article-card-body {
                  border-bottom: none;
                }
              }
            }
            @include media-breakpoint-down(lg){
              &:nth-last-of-type(2){
                .article-card-body {
                  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
                }
              }
            }
          }
          .article-card-body {
            padding-left: 32px;
            .article-card-title {
              margin-bottom: 24px;
              font-size: 18px;
              font-weight: 500;
            }
            .article-card-meta {
              &::before {
                left: -36px;
                font-size: 22px;
              }
            }
            @include media-breakpoint-down(mc){
              padding-left: 0;
            }
          }
        }
      }
    }
  }

  .article-box .article-card, .article-box.layout-7 .article-card{
    margin-bottom: 24px;
  }
}
@include media-breakpoint-down(lg) {
  .banner-with-popular {
    .article-list {
      .article-card {
        &:nth-of-type(6) {
          .article-card-body {
            border-bottom: none;
          }
        }
      }
    }
    .article-card {
      margin-bottom: 24px;
    }
    .left-box {
      width: calc(50% - 12px);
    }
    .right-box {
      width: calc(50% - 12px);
      max-width: 1200px;
    }
    .image-set {
      flex-wrap: wrap;
      .left {
        width: 100%;
        .card-title {
          a {
            font-size: 22px;
          }
        }
      }
      .right {
        width: 100%;
        max-width: 1200px;
      }
    }
  }
}
@include media-breakpoint-down(md) {
  .banner-with-popular {
    flex-wrap: wrap;
    .article-list {
      .article-card {
        &:nth-of-type(5),
        &:nth-of-type(6) {
          display: none;
        }
        &:nth-of-type(3),
        &:nth-of-type(4) {
          .article-card-body {
            border-bottom: none;
          }
        }
      }
    }
    .left-box {
      width: 100%;
    }
    .right-box {
      width: 100%;
    }
    .image-set {
      flex-wrap: wrap;
      .left {
        width: 67.77%;
        max-width: 488px;
        .card-title {
          a {
            font-size: 22px;
          }
        }
      }
      .right {
        width: 28.88%;
        max-width: 208px;
        .article-card {
          margin-bottom: 16px;
        }
      }
    }
  }
}
@include media-breakpoint-down(sm) {
  .banner-with-popular {
    flex-wrap: wrap;
    .article-list {
      .article-card {
        &:nth-of-type(3) {
          .article-card-body {
            border-bottom: 1px solid rgba(0, 0, 0, 0.08);
          }
        }
      }
    }
    .image-set {
      flex-wrap: wrap;
      .left {
        width: 100%;
        max-width: 768px;
        .card-title {
          a {
            font-size: 16px;
          }
        }
      }
      .right {
        display: none;
      }
    }
    .right-box {
      .article-box.layout-5
        .article-list
        .article-card-body
        .article-card-title {
        font-size: 16px;
        @include limit-line(2, 1.33);
      }
    }
  }
}
