.compose-build {
  .popular-down-apps {
  }

  .popular-commercial-picture {
    margin-bottom: $gutter * 6;
  }

  @include media-breakpoint-up(xl) {
    .popular-down-apps {
      padding: $gutter * 2 $gutter * 3 $gutter * 3 $gutter * 3;

      .apps-wrapper,
      .symbol {
        flex-wrap: wrap;
      }

      .symbol {
        .t-lead {
          margin-top: 12px;
        }
        margin-bottom: 20px;
      }
    }

    .popular-commercial-picture {
      .close-btn {
        display: none;
      }
    }
  }
}
