$coverWidth: 0.4194;
$cardBodyWith: 1 - 0.4194;
.article-box.layout-3 {
  .article-card {
    display: flex;
    flex-direction: row-reverse;
  }

  .attrs {
    margin-top: 16px;
    display: inline-block;
  }

  .article-card-title {
    margin-top: 0;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.2em;
  }

  .article-card-cover {
    flex-shrink: 0;
    overflow: hidden;
    width: #{$coverWidth * 100%};
  }

  .article-card-body {
    padding-right: 24px;
    flex: 1;
  }

  @include media-breakpoint-up(xl) {
    .article-card {
      margin-bottom: $gutter * 5;
    }
  }

  @include media-breakpoint-down(sm) {
    .article-card-title {
      font-size: 16px;
      line-height: 1.4em;
    }
    .article-card-body {
      padding-right: 16px;
    }
    .article-card-description {
      display: none;
    }
    .attrs {
      display: none;
    }
  }

  @include media-breakpoint-down(mc) {
    .article-card {
      padding-bottom: 24px;
      border-bottom: 1px solid #ebebeb;
      margin-bottom: 24px;
      &:last-of-type{
        border-bottom: 0;
      }
    }
    .article-card-cover {
      display: none;
    }
    .article-card-body {
      padding-right: 0;
    }
  }
}
