@mixin articleLayout7(){
  .article-card {
    display: flex;
    flex-direction: row;
  }

  .article-card-title {
    margin-top: 0;
    font-size: 16px;
    line-height: 1.33em;
  }

  .article-card-cover {
    flex-shrink: 0;
    overflow: hidden;
    width: 42%;
    margin-right: 16px;
  }
}

.article-box.layout-7 {
  &.mobile-layout{
    @include articleLayout7()
  }
  @media (min-width: 300px) and (max-width: 767.78px) {
    @include articleLayout7()
  }
  @include media-breakpoint-down(mc){
    .article-card-title {
      margin-top: 12px;
      font-size: 16px;
      line-height: 1.33em;
    }
  }
  .article-list{
    @include media-breakpoint-between(md,lg){
      .article-card:nth-of-type(4){
        display: none;
      }
    }
  }
}
