.tabs-slick {
  .tabs-slick-content-box {
    margin-top: 32px;
    @include media-breakpoint-down(md) {
      margin-top: 24px;
    }
  }

  &.tabs-slick-inited {
    .tabs-slick-content-box {
      .tabs-slick-content:not(.current) {
        display: none !important;
      }
    }
  }

  &:not(.tabs-slick-inited) {
    .tabs-slick-content-box {
      .tabs-slick-content:not(:first-of-type) {
        display: none;
      }
    }
    .see-more-box{
      display: none;
    }
  }
  * {
    outline: none !important;
  }
}

.see-more-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.tabs-slick-mobile-nav {
  width: 100%;
  text-align: left;
  padding: 11px 14px;
  background: #e7f5e8;
  font-size: 16px;
  line-height: 1.33em;
  border: none;
  border-radius: 4px;
  position: relative;
  padding-right: 50px;
  &::after {
    content: "\e918";
    font-family: "icomoon" !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    position: absolute;
    right: 18px;
    top: 8px;
    color: #51555c;
    font-size: 28px;
  }
}
@import "./layouts/audio-card";
