.popular-subscribe,
.popular-subscribe-mc4wp {
  h3 {
    font-size: 24px;
  }

  .sub-title {
    display: inline;
  }

  .news-privacy a,
  .more {
    color: #e66d33;
  }

  .more {
    margin-left: $gutter;
  }

  @include media-breakpoint-down(mc) {
    padding-bottom: 60vw;
  }
}

.popular-subscribe {
  // .sub-title,
  // .more {
  //   font-size: 16px;
  // }

  .sub-setup {
    margin-top: $gutter * 3;
  }

  a.btn {
    text-align: center;
    min-height: 44px;
    padding: {
      top: 12px;
      bottom: 12px;
    }

    font-size: 14px;

    &:hover {
      color: $color-white;
      background-color: rgba($color-sky-blue, 0.8);
    }
  }

  @include media-breakpoint-up(xl) {
    .sub-title,
    .more {
      font-size: 18px;
    }
  }
}

.popular-subscribe-mc4wp {
  .sub-setup {
    margin-top: $gutter * 3;
  }

  .mc4wp-form-fields {
    .sub-list {
      overflow: hidden;

      > label {
        margin-bottom: $gutter * 2;
        span {
          font-size: 16px;
        }
      }
    }
  }

  .news-privacy {
    margin-bottom: $gutter * 2;
    span {
      font-size: 16px;
    }
  }

  .sub-email {
    background-color: $color-white;
    border-radius: 6px;
    overflow: hidden;
    height: 42px;
    @include flex(center, space-between);
    padding-left: 14px;
    width: 100%;
    margin-top: $gutter;
    clear: both;

    > label {
      display: none;
    }

    input[type="email"],
    input[type="submit"] {
      border: 0;
    }

    input[type="email"] {
      background-color: transparent;
      width: calc(100% - 60px);
    }

    input[type="submit"] {
      background-color: $color-button;
      font-size: 0;
      width: 56px;
      height: 100%;
      flex-shrink: 0;
      background: {
        image: url(data:image/png;base64,);
        repeat: no-repeat;
        position: center center;
        size: 28px;
      }

      &:hover {
        background-color: $color-button-hover;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .sub-email {
      width: 316px;
    }

    .mc4wp-form-fields {
      .sub-list {
        > label {
          margin-right: $gutter * 3;
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    h3 {
      margin-bottom: $gutter * 2;
    }

    .sub-email {
      width: 390px;
    }
  }

  @include media-breakpoint-between(md, lg) {
    .sub-setup {
      margin-top: $gutter * 2;
    }
  }

  @include media-breakpoint-down(md) {
    .mc4wp-form-fields {
      .sub-list {
        > label {
          width: 100%;
        }
      }
    }
  }

  @include media-breakpoint-down(sm) {
    padding-bottom: 40vw !important;

    h3 {
      text-align: center;
    }
  }

  @media (max-width: 410px) {
    padding-bottom: 56vw !important;
  }
}
