$handle-width: 12px;
$bar-height: 4px;
.range {
  width: 100%;
  background: rgba(#000,0.1);
  height: $bar-height;
  position: relative;
  border-radius: 2px;
  &::after,
  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 8px;
    background: transparent;
    cursor: pointer;
  }
  &::before {
    bottom: $bar-height;
  }
  &::after {
    top: $bar-height;
  }

  .range-fill {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $color-icon;
    width: 40px;
    border-radius: 2px;
  }

  .range-handle {
    position: absolute;
    top: (($handle-width - $bar-height) / 2) * -1;
    left: 0;
    width: $handle-width;
    height: $handle-width;
    padding: 0;
    border-radius: 50%;
    background: $color-icon;
    touch-action: pan-y;
    box-shadow: 0 1px 3px rgba(#000, 0.38);
    z-index: 2;
    transform: translateX(-1px);
    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      background: $color-icon;
      width: $handle-width;
      height: $handle-width;
      transition: transform 0.1s ease;
      z-index: -1;
    }

    &:active {
      &::after {
        transform: translate(-50%, -50%) scale(1.4);
      }
    }
  }

  &.rtl {
    .range-fill {
      left: auto;
      right: 0;
    }
  }
}
