.head-operate {
  position: relative;
  .get-apps {
    margin-right: $gutter * 4;
  }
}

.exhibition-apps {
  width: 350px;
  min-height: 150px;
  height: 150px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.16);
  background-color: #fff;
  position: absolute;
  right: 0;
  top: 54px;
  border-radius: $border-radius * 2;
  overflow: hidden;
  //opacity: 0;
  //visibility: hidden;

  &.show {
    opacity: 1;
    visibility: visible;
  }

  .apps-container {
    padding: $gutter * 3 36px 0 36px;

    .apps-carousel {
      padding-bottom: $gutter * 3;
    }
    .apps-item {
      padding: 0 8px;
      outline: none;

      a {
        outline: none;
      }

      &:nth-of-type(n + 2) {
        display: none;
      }

      &.slick-slide {
        display: block;
      }
    }

    .apps-sign img {
      width: 50px;
      margin-right: $gutter * 2;
    }

    .apps-store {
      margin-top: 10px;
      a {
        width: 50%;
        height: 40px;
        img {
          height: 100%;
        }
        &:nth-of-type(2n -1) {
          padding-right: 6px;
        }
        &:nth-of-type(2n) {
          padding-left: 6px;
        }
      }
    }

    .slick-dots {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: 2;
      padding-bottom: 4px;

      li.slick-active button {
        border-color: $color-brand;
      }
    }

    .slick-arrow {
      opacity: 1;
      background-color: transparent;
      box-shadow: none;
      top: -14px;
      &::before {
        font-size: 40px;
      }

      &.slick-prev {
        left: -38px;
      }

      &.slick-next {
        right: -38px;
      }
    }
  }
}
