.gallery-card {
  position: relative;
  margin-bottom: $gutter * 4;

  .img-item {
    position: relative;
    border-radius: $gutter;
    overflow: hidden;

    img {
      width: 100%;
    }

    .item-info {
      .btn-icon {
        margin: 0 4px;
      }

      .js-fave-btn {
        width: auto;
        padding: 0 8px;
        border-radius: 36px;
        &:hover {
          i {
            color: $color-icon;
          }
        }

        &.like {
          .icon-favorite {
            color: $color-red !important;
          }
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .item-info,
    .card-title {
      position: absolute;
      opacity: 0;
      transition: all 0.35s ease;
    }

    .item-info {
      top: $gutter;
      right: 0;
      transform: translateY(-6px);

      a,
      button {
        background-color: $color-white;
      }
    }

    .card-title {
      bottom: 0;
      left: 0;
      width: 100%;
      transform: translateY(6px);
      padding: 34px 8px 8px 8px;
      background: transparent
        linear-gradient(
          180deg,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.6) 60%,
          rgba(0, 0, 0, 0.85098) 100%
        )
        0% 0% no-repeat padding-box;

      span {
        font-size: 14px;
        color: $color-white;
        @include limit-line(2, 1.3);
      }
    }

    &:hover {
      .item-info,
      .card-title {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  @include media-breakpoint-down(lg) {
    .img-item {
      box-shadow: 0 6px 16px rgba(#000, 0.16);
      .item-info {
        background-color: $color-white;
        padding: $gutter 28px 12px $gutter * 3;

        .js-fave-btn {
          margin-left: auto;
          margin-right: 0;
        }

        a,
        button {
          border: 0;
        }
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .img-item {
      .item-info {
        padding: $gutter;
      }
    }
  }
}
