.tabs-component {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  position: relative;
  & > button {
    width: 40px;
    height: 40px;
    flex-shrink: 0;
  }

  .tabs {
    width: calc(100% - 40px);

    & > .btn {
      padding: 0;
    }

    ul {
      li {
        &.current {
          color: $color-button;
        }

        &:hover {
          color: $color-button;
        }
      }
    }

    .indicator {
      background: $color-button;
    }

    .overflow-arrow {
      i {
        &:hover {
          color: $color-button;
        }
      }
    }
  }

  .popover-box {
    display: none;
    position: absolute;
    right: 0;
    top: 44px;
    width: 660px;
    padding: 32px 32px;
    padding-bottom: 8px;
    background: #fff;
    border-radius: 8px;
    z-index: 99;
    box-shadow: 0 4px 10px rgba(#000000, 0.12);
    .popover-item {
      padding-right: 8px !important;
      padding-left: 8px !important;
      button {
        padding: 12px;
        width: 100%;
        line-height: 1.2em;
      }
      &.current {
        button {
          background-color: #36b049;
          border-color: #36b049;
          color: #fff;
        }
      }
    }

    .popover-item {
      margin-bottom: 24px;
    }

    @include media-breakpoint-down(sm) {
      display: none !important;
    }
  }

  &.popover {
    .popover-btn {
      background: #e7f5e8;
    }
    & > .btn {
      display: none !important;
    }
  }

  @include media-breakpoint-down(sm) {
    .tabs {
      width: 100%;
    }
    .popover-btn {
      display: none !important;
    }
  }
}

.modal {
  z-index: 99999;
  background: rgba(#000000, 0.5);
  .modal-box {
    .modal-body {
      max-height: 50vh;
      overflow: scroll;
      overflow-x: hidden;
      margin-right: 0;
      padding: 0px 15px;
      padding-bottom: 16px;
      button {
        font-size: 16px;
        padding: 14px 0;
        text-align: left;
        line-height: 1.1em;
        &.current {
          color: #2da140;
        }
      }
    }
  }
}

.tabs-modal {
  .modal-body {
    button {
      display: block;
    }
  }
}
