.popular-down-apps {
  border-radius: $border-radius * 2;
  overflow: hidden;
  background-color: $color-card;
  padding: 18px 18px 32px 18px;

  .sym-logo {
    @include flex(center, null, wrap);
    flex-shrink: 0;

    img {
      width: 40px;
      height: 40px;
      background-color: $color-placeholder;
      margin-right: $gutter * 2;
    }

    span {
      font-size: 24px;
      color: $color-brand;
      font-weight: 700;
    }

    &:hover {
      text-decoration: none;
    }
  }

  .t-lead {
    font-size: 16px;
    display: block;
  }

  .badge-list {
    flex-shrink: 0;
    a {
      display: inline-block;
      height: 40px;

      &:nth-of-type(1) {
        margin-right: $gutter * 3;
      }

      img {
        height: 100%;
      }
    }
  }

  @include media-breakpoint-up(md) {
    padding: 28px 46px 28px 38px;

    .apps-wrapper {
      @include flex(center, space-between);
    }

    .symbol {
      padding-right: $gutter * 3;
    }

    .badge-list {
      a {
        &:nth-of-type(1) {
          margin-right: 12px;
        }
      }
    }
  }

  @include media-breakpoint-up(xl) {
    padding: 38px $gutter * 6 35px 80px;

    .symbol {
      @include flex(center);
      .sym-logo {
        img {
          margin-right: $gutter * 3;
        }
        margin-right: $gutter * 4;
      }
    }
  }

  @include media-breakpoint-up(lg) {
  }

  @include media-breakpoint-down(lg) {
    .symbol {
      .t-lead {
        margin-top: 12px;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .badge-list {
      margin-top: 20px;
    }
  }

  @media (max-width: 346px) {
    .symbol {
      .sym-logo {
        span {
          display: block;
          width: 100%;
          text-align: center;
          margin-top: 10px;
          font-size: 22px;
        }
        img {
          margin: 0 auto;
        }
      }

      .t-lead {
        text-align: center;
      }
    }

    .badge-list {
      text-align: center;
      a {
        width: 84%;
        height: auto;

        &:nth-of-type(1) {
          margin: {
            right: 0;
            bottom: 10px;
          }
        }

        img {
          width: 100%;
        }
      }
    }
  }
}

.popular-down-apps.base-map {
  position: relative;
  overflow: unset;
  .apps-wrapper {
    position: relative;
    z-index: 2;
  }

  .bases {
    position: absolute;
    bottom: 0;
    left: 24px;
    z-index: 0;
  }

  @include media-breakpoint-between(md, lg) {
    .apps-wrapper {
      flex-wrap: wrap;
      padding-left: 260px;
      .symbol {
        width: 100%;
        margin-bottom: 14px;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    padding: {
      top: 56px;
      bottom: 56px;
    }

    .apps-wrapper {
      padding-left: 280px;
    }
    .bases {
      left: 60px;
    }
  }
}
