.base-package {
  padding: $gutter * 4 $gutter * 3 34vw $gutter * 3;
  border-radius: $border-radius * 2;
  overflow: hidden;

  background: {
    repeat: no-repeat;
    size: cover;
    position: bottom right;
  }

  h3 {
    line-height: 1.2;
  }

  @include media-breakpoint-up(md) {
    padding: $gutter * 4 200px $gutter * 5 $gutter * 5;
  }

  @include media-breakpoint-up(lg) {
    padding-right: 260px;
  }

  @include media-breakpoint-up(xl) {
    padding: $gutter * 5 400px $gutter * 5 80px;
  }

  @include media-breakpoint-down(sm) {
    h3 {
      text-align: center;
    }
  }

  @include media-breakpoint-down(mc) {
    padding: {
      left: $gutter * 2;
      right: $gutter * 2;
    }
  }
}

.popular-call-action,
.base-package {
  h3 {
    margin-bottom: $gutter * 3;
  }

  @include media-breakpoint-up(lg) {
    h3 {
      font-size: 30px;
    }
  }

  @include media-breakpoint-down(sm) {
    h3 {
      font-size: 24px;
    }

    .desc {
      font-size: 16px;
    }
  }
}
