.audio-basic {
  @include media-breakpoint-between(md, lg) {
    .col-md-4:nth-of-type(4) {
      display: none;
    }
  }

  @include media-breakpoint-down(sm) {
    .col-md-4:nth-of-type(n + 2) {
      .audio-card {
        display: flex;

        .card-img,
        .card-title {
          width: 50%;
        }

        .card-img {
          padding-right: $gutter;
        }

        .card-title {
          padding-left: $gutter;
        }
      }
    }

    .col-md-4:last-child {
      .audio-card {
        margin-bottom: 0 !important;
      }
    }
  }

  @include media-breakpoint-down(mc) {
    .audio-card {
      margin-bottom: $gutter * 3;
    }
    .col-md-4:nth-of-type(n + 2) {
      .audio-card {
        padding-top: $gutter * 3;
        border-top: 1px solid $color-hr;

        .card-img {
          display: none;
        }
        .card-title {
          width: 100%;
          padding: 0;
        }
      }
    }
  }
}
